.media-blocks {
    padding-top: 24px;
}

.media-blocks__title {
    border-bottom: 1px solid $border-color;
    margin-bottom: 0;
    padding: 0 8px 18px;
}

.media-blocks-wide__items {
    position: relative;

}

.media-blocks__item {
    border-top: 1px solid $border-color;
    padding-left: 10px;
    padding-right: 10px;

    &:first-child {
        border-top-color: transparent;
    }
}

.media-blocks__footer {
    border-top: 1px solid $border-color;
    padding: $spacing-base 16px;
    padding-left: 0;
}

@media (min-width: $screen-sm-min) {
    .media-blocks-wide__items:after {
        background: $border-color;
        content: '';
        height: 1px;
        left: 0;
        position: absolute;
        right: 0;
        top: 50%;
    }

    .media-blocks__row {
        @include make-row(40px);
    }

    .media-blocks__item {
        @include make-sm-column(6, 40px);
        border-top-color: transparent;
    }

    .media-blocks-wide__items .media-blocks__item {
        @include make-md-column(4, 40px);
    }
}

@media (min-width: $screen-md-min) {
    .media-blocks-wide__items:after {
        display: none;
    }

    .media-blocks__item {
        border-top-width: 0;
    }
}