.block-list {
    @include clearfix();
    border: solid $block-list-border;
    border-width: 0 1px 1px;
    margin-bottom: $spacing-base;
    padding-left: 0;
}

.block-list__item {
    border: solid $block-list-border;
    border-width: 1px 0 0;
    display: block;
}

.panel {
    > .block-list {
        border-width: 0;
        margin-bottom: 0;
        margin-top: -1px;
    }
}

@media (min-width: $screen-sm-min) {
    .block-list {
        position: relative;
    }

    .block-list__item {
        float: left;
        width: 50%;

        &:after {
            background-color: $block-list-border;
            bottom: 0;
            content: '';
            position: absolute;
            right: 50%;
            top: 0;
            width: 0;
        }

        &:nth-child(odd):after {
            width: 1px;
        }
    }
}

@media (min-width: $screen-md-min) {
    .block-list__item {
        width: 25%;

        &:after {
            width: 1px;
        }

        &:nth-child(1):after {
            right: 25%;
        }

        &:nth-child(3):after {
            right: 75%;
        }

        &:last-child:after {
            width: 0;
        }
    }
}