.popover {
    @include box-shadow(0 1px 5px rgba(187, 187, 187, 0.65));
}

.popover-title {
    border-bottom-width: 0;
}

.popover-title {
    font-size: $font-size-base;
    line-height: 1.5;
    padding: ($spacing-base - 8) ($spacing-base - 2);
}

.popover-content {
    line-height: 1.9;
    padding: ($spacing-base - 8) ($spacing-base - 2);
	word-wrap: break-word;

    .popover-title + & {
        margin-top: -5px;
        padding-top: 10px;
    }
}

.popover--large {
    max-width: 480px;
}