/*
    Navbar Header
    Bevat vanaf:

    Mobiel:
    - Het logo
    - search-toggle
    - menu-toggle

    Tablet/Desktop:
    - Alleen logo

*/
.navbar-header {
    //@include make-row();
    @include make-sm-column(12);
    @include make-md-column(3);

    padding-bottom: 20px;
    padding-top: 20px;
}

.nav-actions {
    @include make-xs-column(6);
    text-align: right;

    @media (min-width: $screen-sm-min) {
        display: none;
    }
}

.nav-search {
    @include make-sm-column(4);
    @include make-md-column(3);

    &.in {
        overflow-y: visible;
    }

    .webform__submit {
        display: none;
    }

    #search-block-form {
        margin-bottom: 15px;
    }

    @media (min-width: $screen-sm-min) {
        position: absolute;
        right: 0;
        top: 40px;
    }
    @media (min-width: $screen-md-min) {
        bottom: -67px; // depending on height of nav_currentlocation
        top: auto;
    }
}

.nav-search__icon {
    font-size: 22px;
    margin-top: -1px;
}

.nav-main-wrapper {
    @include make-sm-column(12);
    @include make-md-column(9);

    //padding-left: 0;
    //padding-right: 0;
}

.nav-main {
    list-style: none;
    overflow-x: visible;
    padding-left: 10px;
    padding-right: 0;

    &.collapsing {
        overflow: hidden;
    }
    &.collapse {
        overflow: visible;
    }

    @media (min-width: $screen-md-min) {
        margin-top: 47px; //TODO: hmm... why?
    }

    /*
        Custom 'theme',
        Generated with: http://smarchal.com/twbscolor/
    */
    $bgDefault      : transparent;
    $bgHighlight    : transparent;
    $colDefault     : $colorGreyDark;
    $colHighlight   : $colorSecondary;

    background-color: $bgDefault;
    border-color: $bgHighlight;
    color: $colDefault;

    > li {
        > a {
            &:hover,
            &:focus {
                background-color: $bgDefault;
                color: $colHighlight;
            }
        }
    }
    > .active {
        > a,
        > a:hover,
        > a:focus {
          background-color: $bgHighlight;
          color: $colHighlight;
        }
    }
    > .open {
        > a,
        > a:hover,
        > a:focus {
            background-color: $bgHighlight;
            color: $colHighlight;
        }
    }

    > li {
        //padding-bottom: 20px;
        position: relative;
        vertical-align: bottom;

        &:hover .nav-main__sub-list {
            display: block;

            @media (max-device-width: $device-max-width) {
                 .touch & {
                    /**
                     * This is not a desktop but most probably a device with touch
                     * screen. We want to hide submenus of the main navigation for
                     * these devices
                     */
                    display: none;
                 }
            }
        }
    }

    // HOME-item:
    li:first-child .nav-main__anchor {
        border-top: 0;
    }

    .label {
        font-size: 10px;
        font-weight: normal;
        margin-left: 8px;
        margin-top: -4px;
        position: absolute;
        top: auto;
    }

    .nav-main__anchor {
        border-top: 1px solid $colorGrey;
        color: $colorGreyDark;
        display: block;
        line-height: 1;
        margin-bottom: 0;
        padding-bottom: 20px;
        padding-left: 0;
        padding-top: 20px;
        position: relative;
        text-align: left;
        text-decoration: none;
        transition: color $transition-duration $transition-timing;

        .emphasis {
            font-weight: normal;
        }

        .icon {
            margin-top: -8px;
            position: absolute;
            right: 8px;
            top: 50%;
        }

        &:hover,
        &:focus {
            color: $colorSecondary;
        }
    }

    @media (min-width: $screen-sm-min) {
        padding-left: 0;
        margin-bottom: 0;

        > li {
            padding-bottom: 20px;

            &:hover .nav-main__sub-list {
                //display: block;
                box-shadow: 0 3px 6px -2px rgba(187, 187, 187, .75);
                max-height: 800px;
                padding-bottom: 5px;
                padding-top: 5px;
            }
        }

        li:first-child .nav-main__anchor {
            padding-left: 0;
        }

        .nav-main__anchor {
            border-top: 0;
            border-right: 1px solid $colorGrey;
            padding-left: 20px;
            padding-right: 20px;
            padding-top: 0;
            padding-bottom: 0;
            margin-right: 0;

            .sub {
                display: block;
                min-height: 1.6em;
                white-space: nowrap;
            }

            .emphasis {
                font-size: $font-size-base;
                font-weight: $font-weight-medium;
                text-transform: uppercase;
            }

            .icon {
                display: none;
            }
        }
    }

    @media (min-width: $screen-md-min) {
        //padding-left: 20px;

        li:first-child .nav-main__anchor {
            padding-left: 20px;
        }
    }

    @media (min-width: $screen-lg-min) {
        .label {
            margin: 0;
            margin-left: 42px;
            top: -10px;

            .is-ie & {
                // Bovenstaande code resetten voor alle versies
                // van Explorer
                margin-left: 8px;
                margin-top: -4px;
                top: auto;
            }
        }
    }
}

.nav-main__sub-list {
    background: #fff;
    border-radius: $border-radius-base;
    font-size: $font-size-base;
    list-style: none;
    margin-top: 20px;
    max-height: 0;
    overflow: hidden;
    padding: 0 20px;
    position: absolute;
    transition: all $transition-duration $transition-timing;
    width: $dropdown-with;
    z-index: $zindex-navbar;
}

.nav-main__sub-anchor {
    border-top: 1px solid $colorGrey;
    color: $colorGreyDark;
    display: block;
    line-height: 1.3;
    padding: 1.1em 1em;
    padding-left: 0;
    position: relative;
    text-decoration: none;
    transition: color $transition-duration $transition-timing;

    .icon {
        margin-top: -8px;
        position: absolute;
        right: 0;
        top: 50%;
    }
}