/* ==========================================================================

    Part: Cloud(s)

    Element(en) met een spreek-ballon-staart.
    - default (groot, paars)
    - text    (klein, voor korte stukken tekst, wit)


    .cloud

    Is:
      Een floatende spreek-ballon

    Context:
      Kan gebruikt worden in verschillende contexten.
      - viewport - floating/fixed aan de browser-viewport rand (left/right) (//TODO: hoe op mobiel?)

    Contains:
      - 1 headline (h1?)
      - (optioneel) 1 sub-headline (h2/3?)
      - 1 call-to-action (btn-alternate)
      - (optioneel) 1 secundaire link-call-to-action (btn-link)

      - Login-widget
      -

    States:
      De 'oorsprong'/'tail' van de ballon kan op alle 4 de hoeken liggen.
      - top-left
      - top-right
      - bottom-left
      - bottom-right

   ========================================================================== */

.cloud {
  border-radius: $border-radius-large;
  position: relative;
  display: block;
}

.cloud__title {
  margin-bottom: 20px;
  margin-top: 0;
}

.cloud--attention {
  $tailSize : 30px;
  @include add-cloud-tail($tailSize : 30px, $tailColor : $colorPrimary, $cloudMargin : 20px);
  @include font-smoothing;

  background-color: $colorPrimary;
  //color: $colorWhite;
  padding: ($spacing-base + $padding-small-horizontal);

  > .h1, > .h2, > .h3,
  > h1, > h2, > h3 {
    color: $colorWhite;
  }
  .ws-truebox {
    background-color: $colorAttentionAlt;
    border-radius: $border-radius-base;
    bottom: 32px;
    color: $colorWhite;
    display: block;
    font-size: $font-size-large;
    height: 165px;
    position: absolute;
    width: 80%;
    padding: 20px;
    .icon {
      float: right;
      font-size: $font-size-base;
    }
  }

}


/*
    Cloud_Text:
*/
.cloud--text {
  padding: 6px 10px;
  background-color: $colorWhite;

  @include add-cloud-tail($tailSize : 15px, $tailColor : $colorWhite, $cloudMargin : 20px);

}

.cloud-margin-correction {
  position: relative;
  top: -22px;
}
