.node-type-news {
  .article-date {
    @include opacity(.5);
    margin: 35px 0px -15px 0px;
  }

  img {
    border-radius: 0px 6px 6px 6px;
  }
  .lead {
    @media (max-width: $screen-sm-max) {
      padding-top: 20px;
      padding-bottom: 10px;
    }
  }
}

/*------------------------------------*\
    EXTENSION SECTION
\*------------------------------------*/

.extension-section {
  margin-top: 40px;

  .col-xs-12 {
    // t.b.v. Tekst blok
    > .col-xs-12 {
      padding: 0;

      > p.h2 {
        margin-top: 0;
      }
    }

    > .trigger-wrapper {
      padding-left: 0;
    }
  }

  // t.b.v. FAQ-Blok
  .panel-default.mt--m {
    margin-top: 0;
  }

  > .row {
    margin-bottom: 20px;
  }

  .tweet-block {
    padding: 0;
  }

  .video-preview {
    max-width: 75%;
    margin-bottom: 0;
  }
}

.node-type-liander-home .extension-section .video-preview {
  margin: auto;
}

.node-type-liander-subhome .extension-section .video-preview {
  margin-bottom: 0;
  max-width: 50%;
}
