.wizard-step-arrow {
  text-align: center;
  line-height: 6px;
  &:before  {
    position: relative;
    top: -8px;
  }
}

.wizard-step {
    padding-top: $spacing-base * 1.5;
    padding-bottom: $spacing-base * 1;

    h2 {
        margin-bottom: 0;
        font-size: 19px;
    }

    .icon-info:before {
        font-size: 18px;
        line-height: 25px;
    }

    .btn-primary {
        margin-bottom: $spacing-base * 2;
    }
}



.wizard-step__image {

    @include center-block();
    border-radius: 15px;
    border-top-left-radius: 0;
    margin-top: $spacing-base;
    max-width: 100%;
    height: auto;
    margin-bottom: 12px;
}

.wizard-step-top {
    max-width: 100%;
    margin-bottom: $spacing-base;
    @include center-block();
}

.wizard-step__alert {
    margin-bottom: 0;
    text-align: left !important;
    display: inline-block;

    &:last-child {
        padding-bottom: 0;
    }
}
div.wizard-step__alert {
  text-align: left !important;
}

.ab-steps {

    margin-bottom: 16px;
    position: relative;

    a.btn-primary {
        position: absolute;
        right: 12px;
        bottom: 12px;
    }
    .panel-body {
        text-align: left;
    }
}

.btn-wizard {
  margin-bottom: 36px;
}


@media (min-width: $screen-xs-min) {

    .wizard-step {

        .wizard-step__image {
            max-width:318px;
        }

    }
}

@media (min-width: $screen-sm-min) {

    .wizard-step {
          margin-left: auto;
          margin-right: auto;
          width: 88%;
          width: 100%;
          position: relative;

          .wizard-step__image {
            margin-top: $spacing-base * 2;
          }

          .wizard-step-top,
          .wizard-step-top.big {
              max-width: 90%;
          }

          .wizard-step-top.small {
              max-width: 360px;
          }

          .row {

            position: relative;

            .left-info {
                position: absolute;
                right: 4%;
                top: 37px;
                padding: 0;
                margin: 0;
                width: 16%;
                height: 204px;
                overflow: hidden;

                //reset to initial values
                .icon.icon-info {
                    position: relative;
                    left: auto;
                    top: auto;
                }
            }
          }
    }
}

@media (min-width: $screen-md-min) {
    .wizard-step-top {
        margin-bottom: $spacing-base * 2;
    }
}

.wizard-step--with-history {
  padding-top: 0;
}

.wizard-step-title {
  margin-top: 0;
  margin-bottom: $spacing-base * 1.5;
}

.wizard-history {
  h2 {
    margin: 0;
    padding: $spacing-base;
    border-bottom: 1px solid $panel-default-border;
  }
  .row {
    margin-left: 0;
    margin-right: 0;
  }
  ul {
    li {
      border-bottom: 1px solid $panel-default-border;
      margin: 0;
      padding: $spacing-base;
      font-size: 14px;
      color: #929292;
      .glyphicon {
        color: #929292;
      }
    }
  }
  @media (min-width: $screen-sm-min) {
    .wizard-history-link-check {
      margin-right: 10px;
    }
  }
}

.wizard-history-links {
  margin-bottom: 0;
}

.wizard-history-link-change {
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}
