.collapsable-container {
    padding: 10px 20px 0;

    .collapsables-list:last-child {
        border-bottom-width: 0;
        margin-bottom: 0;
    }
}

.collapsables-list-title {
    padding-top: 10px;
}

.collapsables-list {
    @extend .list-unstyled;
    margin-bottom: 32px;

    .collapsables-list {
        margin-bottom: 0;
    }

    .list-lined {
        border-bottom-width: 0;
    }

    .collapsable-subtitle {
        display: block;
        font-size: $font-size-base + 1;
        margin-top: 4px;
    }

    .collapsable-title--reversed {
        .collapsable-subtitle {
            margin-left: 34px;
        }
    }

    @media (min-width: $screen-sm-min) {
        .collapsable-subtitle {
            display: inline;
            margin-top: 0;
        }

        .collapsable-subtitle,
        .collapsable-title--reversed .collapsable-subtitle {
            margin-left: 24px;
        }
    }
}

.panel {
    > .collapsables-list {
        border-bottom-width: 0;
        margin: 0 $panel-body-padding;

        > li:first-child,
        > li[style*="display: list-item"] {
            border-top-width: 0;
        }
    }

    .collapsables-list .highlight {
        position: relative;

        &:before {
            background: $colorGreyLighter;
            bottom: 0;
            content: '';
            left: $panel-body-padding * -1;
            position: absolute;
            right: $panel-body-padding * -1;
            top: 0;
        }
    }
}

.collapsable-title {
    cursor: pointer;
    font-weight: $headings-font-weight;
    margin: 0;
    padding: 22px 0 22px 30px;
    position: relative;
    word-wrap: break-word;
}

.compact-collapsibles {
    padding-bottom: floor($spacing-base / 2) - 2;
    padding-top: floor($spacing-base / 2);

    .collapsable-title {
        padding-bottom: floor($spacing-base / 2);
        padding-top: floor($spacing-base / 2);
    }

    .collapsable-content {
        padding-bottom: floor($spacing-base / 2);
    }
}

h5.collapsable-title {
    font-size: $font-size-h4;
    font-weight: $font-weight-regular;

    &.collapsed {
        font-weight: $headings-font-weight;
    }
}

.collapsable-title--reversed {
    padding-left: 0;
    padding-right: 50px;

    .collapsable-icon {
        @include opacity(1);
        color: $colorPrimary;
        left: auto;
        right: 30px;
    }
}

.collapsable-icon {
    @include opacity(.4);
    color: $text-color;
    left: 0;
    margin-top: -13px;
    position: absolute;
    top: 50%;

    &,
    &:before {
        font-size: 22px;
    }
}

.collapsable-icon--closed {
    display: none;
}

.collapsed {
    > .collapsable-icon--closed {
        display: inline-block;
    }
    > .collapsable-icon--opened {
        display: none;
    }
}

.collapsable-content {
    padding-bottom: 24px;
    padding-left: 30px;
}

.faq-level-3 {
    .collapsable-title,
    .collapsable-content {
        padding-left: 42px;
    }
}

.faq-level-4 .collapsable-icon {
    left: 10px;
}

.collapsables-filtered > li {
    display: none;

    &:first-child {
        display: block;
    }
}

@media (min-width: $screen-sm-min) {
    .collapsable-title {
        word-wrap: normal;
    }
}