// Process Container
.process-overview {
  position: relative;
  font-size: $process-font-size;
}

// Process Header
.process-header {
  overflow: auto;
  background-position: center;
  background-size: cover;
}

.process-intro {
  max-width: 400px;
  padding: 2em;
  margin: 4em 0;
  border-radius: 0 6px 6px 0;
}

// Process Line
.process-line {
  position: relative;

  &::after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: (($process-line-container-width - $process-line-width) / 2);
    z-index: 1;
    content: "";
    border-left: $process-line-width solid $process-line-color;

    @media (min-width: $screen-md-min) {
      left: subtract(50%, ($process-line-width / 2));
    }
  }
}

// Process Line Start
.process-start {
  position: relative;
  z-index: 3;
  height: ($process-circle-start-size / 2);

  @media (min-width: $screen-md-min) {
    display: flex;
    justify-content: center;
    height: (($process-circle-start-size + 2em) / 2);
  }
}

// Process Items Container
.process-item-group {
  overflow: hidden;
}

// Process Item Container
.process-item {
  display: flex;

  &:first-of-type {
    .process-line-index {
      margin-top: ($process-card-margin * 2) + $process-card-arrow-top;
    }

    .process-card {
      margin-top: ($process-card-margin * 2);
    }
  }

  &:last-of-type .process-card {
    margin-bottom: ($process-card-margin * 2);
  }

  @media (min-width: $screen-md-min) {
    position: relative;
    left: subtract(50%, ($process-line-container-width / 2));
    max-width: 50%;

    &.reverse {
      left: ($process-line-container-width / 2);
      flex-direction: row-reverse;

      .process-card {
        .arrow {
          right: -$process-card-arrow-width;
          left: auto;

          &::before {
            right: 0;
            left: auto;
            border-width: $process-card-arrow-width 0 $process-card-arrow-width ($process-card-arrow-height / 2);
            border-right-color: transparent;
            border-left-color: $process-card-arrow-outer-color;
          }

          &::after {
            right: $process-card-border-width;
            left: auto;
            border-width: $process-card-arrow-width 0 $process-card-arrow-width ($process-card-arrow-height / 2);
            border-right-color: transparent;
            border-left-color: $process-card-arrow-color;
          }
        }
      }

      .bg-secondary .arrow::after {
        border-right-color: transparent;
        border-left-color: $process-card-secondary-bg;
      }
    }
  }
}

// Process Line Container
.process-line-index {
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: center;
  width: $process-line-container-width;
  min-width: $process-line-container-width;
  margin-top: $process-card-margin + $process-card-arrow-top;

  &.last {
    background-color: $process-bg;

    @media (min-width: $screen-md-min) {
      margin-top: (($process-card-margin + $process-card-arrow-top) - (($process-circle-last-size - $process-circle-size) / 2));

      .process-circle {
        width: $process-circle-last-size;
        height: $process-circle-last-size;
      }
    }
  }
}

// Process Line Circle
.process-circle,
.process-circle-start {
  display: flex;
  align-items: center;
  justify-content: center;
  color: $process-circle-color;
  text-align: center;
  background-color: $process-circle-bg;
  border-radius: 50%;
}

.process-circle {
  width: $process-circle-size;
  height: $process-circle-size;
}

.process-circle-start {
  position: relative;
  top: -($process-circle-start-size / 2);
  width: $process-circle-start-size;
  height: $process-circle-start-size;

  >span {
    font-size: 2em; // fa-2x

    @media (min-width: $screen-md-min) {
      font-size: 3em; // fa-3x
    }
  }

  @media (min-width: $screen-md-min) {
    top: -(($process-circle-start-size + 2em) / 2);
    width: ($process-circle-start-size + 2em);
    height: ($process-circle-start-size + 2em);
  }

  &.sticky {
    position: fixed;
    top: 0;

    @media (min-width: $screen-md-min) {
      width: $process-circle-start-size;
      height: $process-circle-start-size;

      >span {
        font-size: 2em; // fa-2x
      }
    }
  }
}

// Process Line Number Text
.process-index-number {
  font-size: $process-index-number-font-size;
}

// Process Content Container
.process-content {
  margin-left: ($process-line-container-width + $process-card-margin);
  overflow: auto;

  @media (min-width: $screen-md-min) {
    margin: 0 (($process-line-container-width / 2) + $process-card-margin);
  }
}

// Process Item Card
.process-card {
  position: relative;
  display: flex;
  flex: 1 1px;
  flex-direction: column;
  height: 175px;
  margin: $process-card-margin;
  background-color: $process-card-bg;
  border: $process-card-border-width solid $process-card-border-color;
  border-radius: $process-card-border-radius;
  box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, .2);

  &.bg-secondary {
    border: 0;

    .arrow::after {
      border-right-color: $process-card-secondary-bg;
    }
  }

  // Card Arrow
  .arrow {
    position: absolute;
    top: $process-card-arrow-top;
    left: subtract(-$process-card-arrow-width, $process-card-border-width);
    display: block;
    width: $process-card-arrow-width;
    height: $process-card-arrow-height;
    margin: $process-card-arrow-margin 0;

    &::before,
    &::after {
      position: absolute;
      display: block;
      content: "";
      border-color: transparent;
      border-style: solid;
    }

    &::before {
      left: 0;
      border-width: $process-card-arrow-width ($process-card-arrow-height / 2) $process-card-arrow-width 0;
      border-right-color: $process-card-arrow-outer-color;
    }

    &::after {
      left: $process-card-border-width;
      border-width: $process-card-arrow-width ($process-card-arrow-height / 2) $process-card-arrow-width 0;
      border-right-color: $process-card-arrow-color;
    }
  }
}

.process-card-header {
  padding: $process-card-padding $process-card-padding 0;
}

.process-card-section {
  flex-grow: 1;
  overflow: hidden;
}

.process-card-body {
  padding: 0 $process-card-padding;
}

.process-card-footer {
  padding: ($process-card-padding / 2) $process-card-padding;
  border-top: $process-card-border-width solid $process-card-border-color;
  border-radius: 0 0 $process-card-border-radius $process-card-border-radius;
}

.process-card-extend {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
  width: 100%;
}

.process-card-extend-toggle {
  align-items: center;
  justify-content: center;
  transform: translate(50%, 50%);
}
