/*------------------------------------*\
    $FORMULIER IN SUBHOME HEADER
\*------------------------------------*/
.cloud__form--single-field {
    label {
        color: $colorWhite;
        display: block;
        font-size: $font-size-base;
        font-weight: normal;
    }

    .form-control {
        display: inline;
        width: 49%;
    }

    .btn {
        width: 49%;

        .icon {
            display: none;
            float: right;
            position: relative;
            top: 6px;
        }
    }

    @media (min-width: $screen-xs-min) {
        .form-control {
            display: inline;
            width: 69%;
        }

        .btn {
            text-align: left;
            width: 29%;

            .icon {
                display: inline-block;
            }
        }
    }

    @media (min-width: $screen-sm-min) {

        .form-control,
        .btn {
            width: 49%;
        }

    }
}

/*------------------------------------*\
    $DYNAMISCHE MEERDERE VELDEN
\*------------------------------------*/
.dynamic-multiple-field {
    padding-top: $spacing-base / 2;

    .form-control-feedback {
        cursor: pointer;
    }

    .icon {
        background: $swatchPurple;
        border-radius: 50%;
        color: #fff;
        padding: 2px;
        width: auto;
    }

    .form-horizontal & .form-control-feedback {
        right: 0;
        text-align: center;
        top: $spacing-base / 2;
    }
}

.add-dynamic-multiple-field {
    padding-top: $spacing-base / 2;
}

form {
    .form-required {
        display: none;
    }

    .not-required {
        color: darken($swatchGrey, 25%);
        display: block;
        float: left;
        padding-left: 12px;
        padding-top: 7px;
    }
}

/*------------------------------------*\
    $HORIZONTAAL FORMULIER
\*------------------------------------*/
.form-horizontal {

    .form-group {
        @include make-row;

        .control-label {
            @include make-sm-column(4);
            text-align: left;
        }
    }

    .radio label,
    .checkbox label {
        text-align: left;
    }

    .description {
        @include make-xs-column(12);
        @include make-sm-column-offset(3);
        @include make-sm-column(6);
        @include make-md-column-offset(2);
        @include make-md-column(6);
        clear: left;
        padding-top: 8px;
    }

    .description--with-icon {
        padding-left: 34px;

        .icon {
            margin-left: -24px;
            margin-top: 1px;
            position: absolute;

            .is-ie-9 &,
            .is-ie-10 &,
            .is-ie-11 & {
                margin-top: 3px;
            }
        }
    }

    .webform-field {
        @include make-sm-column(6);
    }

    .noLabel .webform-field {
        // We maken dit veld langer omdat er geen label links naast staat
        @include make-sm-column(9);
        @include make-md-column(8);
    }

    .webform-field--wide {
        @include make-sm-column(9);
        @include make-md-column(10);
    }

    .hasInfoText {
        .webform-field {
            padding-right: 45px;
        }
    }

    .noLabel {
        .form-type-checkbox .control-label {
            @include make-xs-column(12);
        }
    }

    .has-feedback .form-control-feedback {
        top: 0;
    }
}

/*------------------------------------*\
    $DATUMVELDEN
\*------------------------------------*/
/**
 * In de selector hieronder is 'form' nodig omdat de stijl van labels anders
 * wordt overschreven door overrides (dat Drupal gebruikt)
 */
form {
    .row--date-fields {
        .col--field {
            padding-left: 0;
            padding-right: 0;
            padding-top: 0;
        }

        .col--divider {
            display: block;
            padding: 6px 0;
        }

        .has-feedback .form-control-feedback {
            right: 0;
        }

        @media (min-width: $screen-xs-min) {
            // We gebruiken binnen deze scope een gutter width van 8px
            @include make-row(8px);

            .col--field {
                @include make-xs-column(5, 8px);
                width: 46%;
            }

            .col--divider {
                @include make-xs-column(2, 8px);
                padding-left: 0;
                padding-right: 0;
                padding-top: 11px;
                text-align: center;
                width: 8%;
            }

            .has-feedback .form-control-feedback {
                right: 5px;
            }
        }
    }
}

/*------------------------------------*\
    $UPLOADVELD
\*------------------------------------*/
.webform-component-managed_file {
    position: relative;

    button.form-submit.btn-default {
      display: none;
    }

    .remove-uploaded {
        float: left;
        margin-left: 8px;
        position: relative;
        top: 7px;
    }

}

/*------------------------------------*\
    $MARKUPVELD
\*------------------------------------*/
.webform-component-fieldset + .webform-component-markup .webform-field {
    padding-bottom: 16px;
}

/*------------------------------------*\
    $FORM FOOTER
\*------------------------------------*/
.form-footer {
    color: darken($swatchGrey, 20%);
    font-size: $font-size-base - 1;
    padding-top: 32px;
}

/*------------------------------------*\
    $RADIO'S & CHECKBOXES
\*------------------------------------*/
.webform-component-radios,
.webform-component-checkboxes {

    .webform-field {

        .form-type-radio,
        .form-type-checkbox {

            float: left;
            margin-left: 20px;
            margin-right: 20px;
            margin-top: 0;
            padding-top: 0;

            label {
                width: auto;
                clear: none;
                padding-left: 0;
                color: rgb(82, 81, 81) !important;

                .trigger8 & {
                    color: #fff !important;
                    padding-left: 21px;
                }

                label {
                    padding: 0;
                }
            }
        }
    }
    &.has-error {
        .form-type-radio,
        .form-type-checkbox {
            label {
                color: $colorAttention !important;
            }
        }
    }
}

.panel--filled-primary {
    div.checkbox {
        padding-left: 0px;

        input {
            margin: 0;
            padding: 0;
            margin-right: 8px;
            margin-left: -21px;
        }

        label {
            width: auto;
            padding: 0 0 0 22px;
            display: block;
            line-height: 14px;
        }
    }
}

// specific sms aanmeld styling
#webform-component-huisnummer, #webform-component-toevoeging {
    display: block;
}

#webform-component-huisnummer label, #webform-component-toevoeging label {
    display: none;
}

#webform-client-form-20 .col-sm-3 {
    width: 26%;
}

.trigger8 {

    .not-required,
    .alert-danger {
        display: none !important;
    }


    .ws-errorbox {
        @include css-arrow(topleft, $swatchOrange, 8px);
        background: $swatchOrange;
        border-radius: $border-radius-base;
        color: #fff;
        //margin-left: 10px;
        margin-top: 18px;
        overflow: visible;
        padding: 7px 13px;
        position: relative;

        .help-block {
            color: #fff;
        }
    }

    .webform-component-phonefield .ws-errorbox {
        max-width: 300px;
    }

    .future-disturbances {

        .ws-errorbox {
            margin-left: 0px;
            width: auto;
            @include css-arrow(topleft, $swatchOrange, 8px);
            clear: both;
            height: auto;
            margin-top: 18px;
        }

        div.col-sm-4 div.form-group {

            div#webform-component-toevoeging {
                position: absolute;
                left: 69px;
            }

            div {
                margin-right: -2px;
            }

            div > div {
                float: left;
            }

        }
    }

}

// Feedback icon (requires .glyphicon classes)
.form-control-feedback {
    position: absolute;
    top: ($line-height-computed + 5); // Height of the `label` and its margin
    right: 0;
    z-index: 3; // Ensure icon is above input groups
    display: block;
    width: $input-height-base;
    height: $input-height-base;
    line-height: $input-height-base;
    text-align: center;
    pointer-events: auto;
}

// Wachtwoord vergeten link
form#user-login #edit-link {
    display: block;
    margin-bottom: 16px;
}
