/*
    'Fix' font-rendering on OSX/iOS:
*/
@mixin font-smoothing($state : on) {
    @if $state == off {
        -webkit-font-smoothing: subpixel-antialiased;
        -moz-osx-font-smoothing: auto;
    } @else {
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

@mixin vendor-webkit($property, $value...){
    -webkit-#{$property}:$value;
            #{$property}:$value;
}

/*
    Add Cloud-Tail:
*/
@mixin add-cloud-tail($tailSize : 10px, $tailColor : $colorPrimary, $cloudMargin : 10px) {

  &:before {
    content: "";
    position: absolute;
    display: block;
    width: $tailSize;
    height: $tailSize;

    color: $tailColor;
    font-family: $icon-liander-font-name;
    font-size: $tailSize;
    line-height: 1;

    pointer-events: none;
  }

  &.bottom-left {
    margin-bottom: $cloudMargin + $tailSize;
    border-bottom-left-radius: 0;

    &:before {
      @extend .icon-cloud-tail-bottom-left:before;
      bottom: ($tailSize) * -1;
      left: 0;
    }
  }
  &.bottom-right {
    margin-bottom: $cloudMargin + $tailSize;
    border-bottom-right-radius: 0;

    &:before {
      @extend .icon-cloud-tail-bottom-right:before;
      bottom: ($tailSize) * -1;
      right: 0;
      left: auto;
    }
  }

  &.top-left {
    margin-top: $cloudMargin + $tailSize;
    border-top-left-radius: 0;

    &:before {
      @extend .icon-cloud-tail-top-left:before;
      top: ($tailSize - 2) * -1;
      bottom: auto;
      left: 0;
    }
  }
  &.top-right {
    margin-top: $cloudMargin + $tailSize;
    border-top-right-radius: 0;

    &:before {
      @extend .icon-cloud-tail-top-right:before;
      top: ($tailSize - 2) * -1;
      bottom: auto;
      right: 0;
      left: auto;
    }
  }
}

//cross browser border-radius mixin
@mixin rounded($radius: 2px) {
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  border-radius: $radius;
  -moz-background-clip: padding; -webkit-background-clip: padding-box; background-clip: padding-box;
}

// Generate the very small column

@mixin make-vs-column($columns, $gutter: $grid-gutter-width) {
  position: relative;
  min-height: 1px;
  padding-left:  ($gutter / 2);
  padding-right: ($gutter / 2);

  @media (min-width: $screen-vs-min) {
    float: left;
    width: percentage(($columns / $grid-columns));
  }
}
@mixin make-vs-column-offset($columns) {
  @media (min-width: $screen-vs-min) {
    margin-left: percentage(($columns / $grid-columns));
  }
}
@mixin make-vs-column-push($columns) {
  @media (min-width: $screen-vs-min) {
    left: percentage(($columns / $grid-columns));
  }
}
@mixin make-vs-column-pull($columns) {
  @media (min-width: $screen-vs-min) {
    right: percentage(($columns / $grid-columns));
  }
}

@mixin info-message-position($position) {
    @if $position == top or $position == top-left or $position == top-right {
        padding: $tooltip-arrow-width 0;

        .info-message__arrow {
            @include info-message-arrow-position($position);
        }
    } @else if $position == right {
        padding: 0 $tooltip-arrow-width;

        .info-message__arrow {
            @include info-message-arrow-position($position);
        }
    } @else if $position == bottom or $position == bottom-left or $position == bottom-right {
        padding: $tooltip-arrow-width 0;

        .info-message__arrow {
            @include info-message-arrow-position($position);
        }
    } @else if $position == left {
        padding: 0 $tooltip-arrow-width;

        .info-message__arrow {
            @include info-message-arrow-position($position);
        }
    }
}

@mixin info-message-arrow-position($position) {
    @if $position == top {
        bottom: 0;
        left: 50%;
        right: auto;
        top: auto;
        margin-left: -$tooltip-arrow-width;
        border-width: $tooltip-arrow-width $tooltip-arrow-width 0;
        border-top-color: $colorAttention;
    } @else if $position == top-left {
        bottom: 1px solid green;
        left: $tooltip-arrow-width * 2;
        right: auto;
        top: auto;
        border-width: $tooltip-arrow-width $tooltip-arrow-width 0;
        border-top-color: $colorAttention;
    } @else if $position == top-right {
        bottom: 0;
        left: auto;
        right: $tooltip-arrow-width * 2;
        top: auto;
        border-width: $tooltip-arrow-width $tooltip-arrow-width 0;
        border-top-color: $colorAttention;
    } @else if $position == right {
        bottom: auto;
        left: 0;
        right: auto;
        top: 50%;
        margin-top: -$tooltip-arrow-width;
        border-width: $tooltip-arrow-width $tooltip-arrow-width $tooltip-arrow-width 0;
        border-right-color: $colorAttention;
    } @else if $position == left {
        bottom: auto;
        left: auto;
        right: 0;
        top: 50%;
        margin-top: -$tooltip-arrow-width;
        border-width: $tooltip-arrow-width 0 $tooltip-arrow-width $tooltip-arrow-width;
        border-left-color: $colorAttention;
    } @else if $position == bottom {
        bottom: auto;
        left: 50%;
        right: auto;
        top: 0;
        margin-left: -$tooltip-arrow-width;
        border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;
        border-bottom-color: $colorAttention;
    } @else if $position == bottom-left {
        bottom: auto;
        left: $tooltip-arrow-width * 2;
        right: auto;
        top: 0;
        border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;
        border-bottom-color: $colorAttention;
    } @else if $position == bottom-right {
        bottom: auto;
        left: auto;
        right: $tooltip-arrow-width * 2;
        top: 0;
        border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;
        border-bottom-color: $colorAttention;
    }
}


@mixin css-arrow($arrow-position, $arrow-color, $arrow-size) {

    &:after {
      position: absolute;
      border: solid transparent;
      content: " ";
      top: auto;
      left: auto;
      height: 0;
      width: 0;
      margin: 0;
      border-color: transparent;
      border-width: $arrow-size;
      pointer-events: none;
    }
    @if $arrow-position == "topleft" {
      &:after {
        bottom: 100%;
        border-bottom-color: $arrow-color;
        margin-left: (-$arrow-size);
        left: 26px;
      }
    }
    @if $arrow-position == "top" {
      &:after {
        bottom: 100%;
        border-bottom-color: $arrow-color;
        margin-left: (-$arrow-size);
        left: 50%;
      }
    }

    @if $arrow-position == "topleft" {
      &:after {
        bottom: 100%;
        border-bottom-color: $arrow-color;
        margin-left: (-$arrow-size);
        left: 26px;
      }
    }

    @if $arrow-position == "right" {
      &:after {
        left: 100%;
        border-left-color: $arrow-color;
        margin-top: (-$arrow-size);
        top: 50%;
      }
    }
    @if $arrow-position == "bottom" {
      &:after {
        top: 100%;
        border-top-color: $arrow-color;
        margin-left: (-$arrow-size);
        left: 50%;
      }
    }
    @if $arrow-position == "left" {
      &:after {
        right: 100%;
        border-right-color: $arrow-color;
        margin-top: (-$arrow-size);
        top: 50%;
      }
    }
}

/**
 * Mixin to reset the styles of Bootstrap's .sr-only:
 *
 * position: absolute;
 * width: 1px;
 * height: 1px;
 * margin: -1px;
 * padding: 0;
 * overflow: hidden;
 * clip: rect(0,0,0,0);
 * border: 0;
 */
@mixin reset-sr-only() {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  padding: 0;
  position: static;
  width: auto;
}

@mixin animation-play-state($play-state) {
  -webkit-animation-play-state: $play-state;
  -moz-animation-play-state: $play-state;
  animation-play-state: $play-state;
}

@mixin border-brand-radius($radius: $border-radius-base) {
    border-radius: 0 $radius $radius;
}

@mixin trigger-variant($bg-color, $text-color: #fff) {
  background-color: $bg-color;

  .panel-footer {
    background-color: $bg-color;
  }

  .panel-title,
  .trigger-subtitle,
  .panel-body,
  .panel-body a {
    color: $text-color;
  }
}
