/*------------------------------------*\
    $SEARCH FORM
\*------------------------------------*/
.form--site-search {
    .form-control {
        padding-left: 15px;

        &::-moz-placeholder           { font-size: 12px; }
        &:-ms-input-placeholder       { font-size: 12px;  } // Internet Explorer 10+
        &::-webkit-input-placeholder  { font-size: 12px; padding-top: 3px; } // Safari and Chrome

        @media (min-width: $screen-xs-min) {
            &::-moz-placeholder           { font-size: $font-size-medium; }
            &:-ms-input-placeholder       { font-size: $font-size-medium;  } // Internet Explorer 10+
            &::-webkit-input-placeholder  { font-size: $font-size-medium; padding-top: 0; } // Safari and Chrome
        }

        @media (min-width: $screen-sm-min) {
            padding-left: 28px;
        }
    }

    .input-group-lg .input-group-addon {
        padding-bottom: 0;
        padding-top: 0;

        .icon-search {
            left: -3px;
            position: relative;
        }
    }

    .row .form-control {
        margin-bottom: 10px;
    }

    .list-group-item--search {
        padding-left: 15px;
        padding-right: 15px;
    }

    .icon-cancel-circled:before {
        font-size: 26px;
    }

    @media (min-width: $screen-sm-min) {
        .list-group-item--search {
            padding-left: 30px;
            padding-right: 30px;
        }

        .row .form-control {
            margin-bottom: 0;
        }
    }
}

/*------------------------------------*\
    $CLOUD SEARCH
\*------------------------------------*/
.cloud--search-site {
    padding-left: 15px;
    padding-right: 15px;

    @media (min-width: $screen-xs-min) {
        padding-left: 30px;
        padding-right: 30px;
    }
}

/*------------------------------------*\
    $SEARCH RESULTS IN DROPDOWN
\*------------------------------------*/
.list-results {
    margin-top: -1px;
    position: absolute;
    width: 100%;
    z-index: 2;

    .list-group-item:first-child {
        @include border-top-radius(0);
    }
}

/*------------------------------------*\
    $SEARCH RESULTS
\*------------------------------------*/
.search-results-title {
    margin-bottom: 10px;
    margin-top: 10px;
    padding-left: 30px;
    padding-right: 30px;
	color: $colorGreyDark;

    .list-group-item--search & {
        padding-left: 0;
        padding-right: 0;
    }
}

.search-result {
    display: block;
    margin: -10px -30px;
    padding: 18px 40px 18px 30px;
    position: relative;
    text-decoration: none;
    transition: background-color $transition-duration $transition-timing;

    &:hover,
    &:focus {
        background-color: $colorGreyLight;
    }

    .title {
        color: $colorPrimary;
        font-size: $font-size-h3 - 2;
        margin-top: 0;
    }

    .breadcrumb {
        @include font-smoothing(off);
        background-color: transparent;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

		> li {
            display: inline;
			font-size: $font-size-base - 1;
		}

        > li:after {
            color: $colorSecondary;
			font-size: 8px;
        }
    }

    .icon-arrow-right {
        color: $colorGrey;
        margin-top: -10px;
        position: absolute;
        right: 30px;
        top: 50%;
    }

    /**
     * Zoekresultaat binnen het zoekformulier (lijst met ajax-resultaten)
     */
    .form--site-search & {
        margin-left: -15px;
        margin-right: -15px;
        padding-left: 15px;
        padding-right: 25px;

        .title {
            font-size: $font-size-h5;
            margin-bottom: 0;
            padding-bottom: 2px;
            padding-top: 2px;
        }

        .icon-arrow-right {
            right: 15px;
        }

        @media (min-width: $screen-sm-min) {
            margin-left: -30px;
            margin-right: -30px;
            padding-left: 30px;
            padding-right: 40px;

            .title {
                font-size: $font-size-h4;
                margin-bottom: 4px;
                padding-bottom: 0;
                padding-top: 0;
            }

            .icon-arrow-right {
                right: 30px;
            }
        }
    }
}

@media (max-width: ($screen-xs-min - 1)) {
	.result-link {
		text-align: right;
	}
}

.spelling-suggestions {
    color: $colorPrimary;
    font-size: $font-size-h4;
    padding: 0 ($spacing-base + $padding-small-horizontal);

    dt,
    dd {
        display: inline;
    }

    dt strong {
        font-weight: normal;

        &:after {
            content: ':';
        }
    }

    dd {
        font-weight: $font-weight-medium;
        margin-left: .4em;
    }
}

.mobile-search .search-icon {
    border: none;
    padding: 0 12px;
}

.mobile-header .row.nav ul li a {
    text-decoration: none;
}
