.track-n-trace {
  color: #929292;
  height: 82px;
  margin: 10px 0;
  position: relative;
}

.track-n-trace-bar {
  background-color: #f5f5f5;
  height: 10px;
  left: 26px;
  position: absolute;
  right: 26px;
  top: 21px;
}

.track-n-trace-filler {
  background-color: #e56c00;
  height: 10px;
  left: 26px;
  max-width: calc(100% - 52px);
  position: absolute;
  top: 21px;
}

.track-n-trace-icon {
  background-color: #d7d7d7;
  border-radius: 26px;
  color: #fff;
  height: 52px;
  line-height: 35px;
  padding: 7px;
  width: 52px;

  @media (max-width: $screen-xs-max) {
    border-radius: 18px;
    color: transparent;
    height: 35px;
    margin-top: 8px;
    width: 35px;
  }
}

.track-n-trace-label {
  height: 30px;
  padding-top: 8px;
  width: 52px;
  white-space: nowrap;

  @media (max-width: $screen-xs-max) {
    display: none;
  }
}

.track-n-trace-step {
  position: absolute;

  &.active {
    .track-n-trace-label {
      display: inline-block;
    }

    @media (max-width: $screen-xs-max) {
      .track-n-trace-icon {
        border-radius: 26px;
        color: #fff;
        height: 52px;
        margin-top: 0;
        width: 52px;
      }
    }
  }
}

.track-n-trace-step.active ~ .track-n-trace-step {
  .track-n-trace-icon {
    background-color: #d7d7d7;
  }
}

.track-n-trace-step:first-child {
  left: 0;
}

.track-n-trace-step:last-child {
  right: 0;
}

/* three items */
.track-n-trace-step:first-child:nth-last-child(3) ~ .track-n-trace-step:nth-child(2) {
  left: calc(50% - 26px);
}

/* four items */
.track-n-trace-step:first-child:nth-last-child(4) ~ .track-n-trace-step:nth-child(2) {
  left: calc(33.3333% - 18px);
}

.track-n-trace-step:first-child:nth-last-child(4) ~ .track-n-trace-step:nth-child(3) {
  right: calc(33.3333% - 18px);
}

.track-n-trace-step:first-child:nth-last-child(3) ~ .track-n-trace-step:nth-child(2) .track-n-trace-label span,
.track-n-trace-step:first-child:nth-last-child(4) ~ .track-n-trace-step:nth-child(2) .track-n-trace-label span,
.track-n-trace-step:first-child:nth-last-child(4) ~ .track-n-trace-step:nth-child(3) .track-n-trace-label span {
  display: inline-block;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.track-n-trace-step:last-child .track-n-trace-label span {
  display: inline-block;
  position: absolute;
  right: 0;
}

.track-n-trace-warning {
  .track-n-trace-filler {
    background-color: #e56c00;
  }

  .track-n-trace-icon {
    background-color: #e56c00;
  }

  .track-n-trace-step.active .track-n-trace-label {
    color: #e56c00;
  }
}

.track-n-trace-success {
  .track-n-trace-filler {
    background-color: #7db239;
  }

  .track-n-trace-icon {
    background-color: #7db239;
  }

  .track-n-trace-step.active .track-n-trace-label {
    color: #7db239;
  }
}
