.download {
    display: block;
    font-size: $font-size-base - 1;
    overflow: hidden;
    padding-left: 30px;
    padding-top: 6px;
    position: relative;

    .icon {
        left: 0;
        margin-top: 2px;
        position: absolute;
    }

    @media (min-width: $screen-sm-min) {
        overflow: visible;
    }
}