.label-heading {
    margin-top: 0;
    text-align: center;

    .label {
        margin-bottom: $padding-base-vertical;
    }
}

.label-heading--align-left {
    padding-left: 40px;
    position: relative;
    text-align: left;

    .label {
        left: 0;
        position: absolute;
        top: 0;
    }
}

.label-heading__wrapper {
    display: block;
    margin-bottom: 8px;
}

/*------------------------------------*\
    $ICONED HEADING
\*------------------------------------*/
.iconed-heading {
    margin-top: 0;
    position: relative;
}

.iconed-heading__icon {
    color: $colorPrimary;
    font-size: 120%;
    left: 0;
    position: absolute;
    margin-top: -2px;
}

.iconed-heading__text {
    display: block;
    text-indent: 1.6em;
}