.navbar-nav-item {
  padding-bottom: 20px;

  &:hover {
    .navbar-nav-submenu {
      display: block;
      max-height: 800px;
      padding-top: 5px;
      padding-bottom: 5px;
    }
  }

}

.navbar-nav-submenu {
  max-height: 0;
  background-color: white;
  position: absolute;
  list-style: none;
  margin: 20px 0 0 0;
  padding: 0 $spacing-base;
  overflow: hidden;
  width: 244px;
  box-shadow: 0 3px 6px -2px rgba(187,187,187,.75);
  transition: all .2s ease-in-out;
  z-index: 1000;

  > li {
    text-transform: none;
    display: block;
    margin: 0;
    padding: 0;
  }
}

.container-top-menu .navbar-nav .navbar-nav-item .navbar-nav-submenu > li > a {
  display: block;
  font-family: $font-family-base;
  font-size: 17.25px;
  margin: 0;
  padding: $spacing-base 0 !important;
  border-color: $colorGreyLight;
  border-width: 1px 0 0 0 !important;
  border-style: solid;
}
