.nav-breadcrumbs {
    background-color: $swatchBeige;
    border-bottom: 1px solid $colorGrey;
    border-top: 1px solid $colorGrey;
    min-height: 64px; // Height of search-box!
    padding-bottom: 13px;
    padding-top: 15px;
}

.breadcrumb {
    margin-bottom: 0;
    padding-left: 7px;
    padding-top: 12px;

    .nav-breadcrumbs & {
        margin-left: -10px;
    }

    a {
        text-decoration: none;

        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }

    .long-title {
        display: none;
    }

    // HOME-icon:
    .icon {
        font-size: $font-size-small;
        padding: 10px;
        padding-right: 0;
        top: -1px;
    }

    > li {
        font-weight: 100;
        + li:before {
			content: "";
			display: none;
		}
    }

    > li:after {
      @extend .icon-arrow-light-right:before;
            color: $breadcrumb-color;
            font-family: $icon-liander-font-name;
            font-size: 10px;
            margin-left: 18px;
            margin-right: 12px;
	}

    > li.last:after,
	> li:last-of-type:after{
			content: "";
			display: none;
	}

    li.active {
        @extend .sr-only;
    }

    .page-name--home {
        margin-left: 8px;
    }

    @media (min-width: $screen-sm-min) {
        li.active {
            @include reset-sr-only();
            display: inline-block;
        }

        .long-title {
            display: inline;
        }

        .short-title {
            display: none;
        }
    }

    @media (min-width: $screen-md-min) {
        .nav-breadcrumbs & {
            padding-right: 240px;
        }
    }
}
