.layover,
.layover-container {
    background: rgba(0, 0, 0, .4);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
}

.layover {
    position: fixed;
    transition: opacity $transition-duration $transition-timing;
    z-index: 2;
}

.layover-container {
    position: absolute;
}

.layover--hidden {
    @include opacity(0);
}