.sr-only, .breadcrumb li.active {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.container-header {
  max-width: 1280px;
  margin: 0 auto;
  background-color: #fff;
}

.container-header.sticky-menu {
  max-width: 100%;
}

/**
 * Sticky Menu.
 *
 * The stickyness of the menu is complicated:
 *  * user can configure stickyness to be on/off for all responsive version
 *  * and specifically disable the desktop stickyness
 *  * the :not(.navbar-no-desktop) and @media queries below handle this
 */
.navbar-is-fixed-top:not(.navbar-no-desktop) .sticky-menu {
  position: fixed;
  top: 0;
  -webkit-box-shadow: 0 2px 3px -2px #929292;
  box-shadow: 0 2px 3px -2px #929292;
}

.navbar-is-fixed-bottom:not(.navbar-no-desktop) .sticky-menu {
  position: fixed;
  bottom: 0;
  -webkit-box-shadow: 0 2px 3px -2px #929292;
  box-shadow: 0 2px 3px -2px #929292;
}

.navbar-is-static-top .sticky-styleable {
  background-color: #fff;
}

.navbar-is-fixed-top:not(.navbar-no-desktop) .sticky-menu,
.navbar-is-fixed-bottom:not(.navbar-no-desktop) .sticky-menu {
  width: 100%;
  background-color: #fff;
  z-index: 10000;
}

.navbar-is-fixed-top:not(.navbar-no-desktop) .sticky-menu .container-top-menu .main-menu,
.navbar-is-fixed-bottom:not(.navbar-no-desktop) .sticky-menu .container-top-menu .main-menu {
  margin-top: 20px;
}

