html,
body {
  min-height: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  margin: 0;
  min-height: 100%;
  padding: 0;
  position: relative;
  overflow-x: hidden;
}

/* Behaviour
--------------------------------------------------------------*/
html {
  -webkit-tap-highlight-color: rgba(0, 140, 190, 0.33);
  overflow-y: initial;
}

html.is-ie-9 {
  overflow-y: auto;
}

::-moz-selection {
  background-color: rgba(0, 140, 190, 0.66);
  color: #fff;
  text-shadow: none;
}

::selection {
  background-color: rgba(0, 140, 190, 0.66);
  color: #fff;
  text-shadow: none;
}

/* Hyperlinks
--------------------------------------------------------------*/
a,
.anchor {
  text-decoration: underline;
  /* Fix for removing underline at links that contain steps or columns */
}

a.active, a:active, a:hover, a:focus,
.anchor.active,
.anchor:active,
.anchor:hover,
.anchor:focus {
  text-decoration: none;
}

a:focus,
.anchor:focus {
  outline: 0;
}

a:link[href^="#"],
.anchor:link[href^="#"] {
  text-decoration: none;
}

/* Skiplinks
--------------------------------------------------------------*/
.skiplinks {
  height: 0;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.skiplink {
  display: block;
  margin: 0;
  padding: 1em;
  position: absolute;
  top: -5em;
  width: auto;
  z-index: 10000;
}

.skiplink:focus {
  background-color: #821E7D;
  color: #fff;
  top: 0;
}

/* Site header on desktop & mobile
--------------------------------------------------------------*/
.container-header .dropdown-menu,
.mobile-header .dropdown-menu {
  width: 270px;
}

.container-header .dropdown .dropdown-menu > li > a,
.mobile-header .dropdown .dropdown-menu > li > a {
  color: #525151;
  font-size: 11px;
}

.container-header .dropdown .dropdown-menu > li > a .apachesolr_autocomplete.message,
.mobile-header .dropdown .dropdown-menu > li > a .apachesolr_autocomplete.message {
  font-size: 10px;
}

.container-header .form-control-feedback,
.mobile-header .form-control-feedback {
  filter: alpha(opacity=50);
  opacity: 0.5;
}

/* Site logo
--------------------------------------------------------------*/
.site-logo {
  position: relative;
  float: left;
  width: 50%;
  min-height: 1px;
  padding-right: 10px;
  padding-left: 10px;
  position: relative;
  min-height: 1px;
  padding-right: 10px;
  padding-left: 10px;
  margin-bottom: 0;
  margin-top: 0;
  position: relative;
  z-index: 1;
}

@media (min-width: 768px) {
  .site-logo {
    float: left;
    width: 100%;
  }
}

.site-logo__anchor {
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
  display: block;
  float: left;
  max-width: 90px;
  position: relative;
  z-index: 2;
}

@media (min-width: 768px) {
  .site-logo__anchor {
    max-width: 110px;
  }
}

@media (min-width: 992px) {
  .site-logo__anchor {
    max-width: 146px;
  }
}

/* Global footer
--------------------------------------------------------------*/
.global-footer {
  border-top: none;
  padding: 20px 0;
}

/* Global header

    Deze <section> elementen overspannen de volledige browser-viewport.
    Ze bevatten altijd een `.container` element om een grid-row erbinnen te centereren/groeperen.
    Achtergrond-kleuren en padding zijn te varieren d.m.v. enkele classes:
--------------------------------------------------------------*/
.page-section {
  padding-bottom: 20px;
  padding-top: 20px;
}

.page-section:before, .page-section:after {
  display: table;
  content: " ";
}

.page-section:after {
  clear: both;
}

.section-first {
  padding-top: 0;
}

.section-primary {
  background-color: transparent;
}

.section-secondary {
  background-color: #EEE;
}

/* 2 Kolommen
    ___ ___ ___ ___ ___ ___ ___ ___ ___ ___ ___ ___
   | Side (3)  | Body (9)                          |
   |           |                                   |
   |___ ___ ___|___ ___ ___ ___ ___ ___ ___ ___ ___|

    .page-side komt in de source EERST, vervolgens .page-body
--------------------------------------------------------------*/
.section-side {
  padding-top: 0;
  padding-bottom: 0;
}

@media (min-width: 992px) {
  .page-side {
    padding-right: 0;
  }
}

