.the-twist {
  -webkit-animation: shakeHorizontal 1s 0s ease-in-out infinite;
  -o-animation: shakeHorizontal 1s 0s ease-in-out infinite;
  animation: shakeHorizontal 1s 0s ease-in-out infinite;
  -webkit-animation-play-state: paused;
  -moz-animation-play-state: paused;
  animation-play-state: paused;
  display: none;
  max-width: 98px;
  padding-left: 72px;
