/**
 * Footer
 */

footer.footer {
    background: $swatchGreyMediumLight;
    border-top: none;
    padding-top: 0;
    padding-bottom: 15px;
    font-size: $font-size-footer;

    .footerlogo {
        max-width: 115px;
    }

    .slogan {
        margin-top: 3px;
        color: $swatchPurple;
        @include fontLight();
        font-weight: $font-weight-lighter;
        font-size: $font-size-medium;
    }

    .global-footer__to-top {
        @include border-top-radius($border-radius-large);
        background-color: $swatchGreyMediumLight;
        position: absolute;
        padding-top: $padding-base-vertical + 3;
        right: $grid-gutter-width * 1.5;
        top: -34px;
        white-space: nowrap;
        color: $swatchGreyMediumDark;
        @include fontLight();
        font-weight: $font-weight-lighter;

        &:link[href^="#"] .icon {
            width: auto;
        }
    }

    div.sitemap {
        padding: 0;
        a.sitemap-toggle {
            color: $swatchPurple;
            font-size: $font-size-medium;
            text-decoration: none;
            @include fontLight();
            font-weight: $font-weight-lighter;
            &:hover {
                border-bottom: none;
            }
        }

        .section-header {
            color: $swatchPurple;
            @include fontLight();
            font-weight: $font-weight-lighter;
        }

        .sitemap-col {
            width: 235px;
            float: left;
        }
    }

    .btn{
        @include box-shadow(none);
        &:hover {
            color: $swatchPurple;
        }
    }

    .text-primary {
        color: $swatchGreyDark;
        font-size: $font-size-base;
    }

    ul.footer-menu {
        list-style-type: none;
        padding-left: 0;
        li a {
            color: $swatchGreyDark;
            line-height: 30px;
            @include fontLight();
            font-weight: $font-weight-lighter;
        }
    }

    .region-footer-social {
        ul.social-media-links {
            text-align: right;
        }
        ul li a {
            font-size: $font-size-base;
            border-left: none;
        }
        ul li>a:first-child {
            margin-right: 5px;
        }
    }

    .footer-links {
        padding-top: 40px;
        a {
            color: $swatchGreyMediumDark;
            text-decoration: none;
            &:hover {
                color: $swatchPurple;
            }
        }
        .footer-logo-slogan {
            padding: 0 15px 40px 15px;
        }
        .nav-main__anchor h4 {
            @include fontBold();
            font-weight: $font-weight-regular;
        }
        div {
            padding: 0;
        }
    }

    .copyright-container {
        color: $swatchGreyMediumDark;
        padding: 15px;
        span.copyright-text {
            padding-left: 0;
        }
        span {
            padding: 1px 15px 0 15px;
            border-right: 1px solid $swatchGreyMediumDark;
            &.last {
                border-right: 0;
            }
            a {
                color: $swatchGreyMediumDark;
                text-decoration: none;
                &:hover {
                    border-bottom: solid 1px $swatchGreyMediumDark;
                    display: inline;
                    padding-bottom: 3px;
                }
            }
        }
        .text-container {
            padding: 10px 0;
        }
    }

    .social-container {
        padding: 0;
        ul li {
            padding: 0 0 2px 0;
            &.first {
                padding-left: 0;
            }
            &.last {
                padding-right: 0;
                a {
                    margin-right: 0;
                }
            }
        }

        .block-social-media-links {
            float: right;

            .social-media-links a img {
                width: 34px;
                height: 34px;
            }
        }
    }
}

@media (max-width: $screen-desktop) {
    footer.footer {
        .social-container {
            .block-social-media-links {
                padding-top: 5px;
                float: left;
            }
        }
        div.sitemap {
            .sitemap-col {
                width: 192px;
            }
        }
    }
}

@media (max-width: $screen-tablet) {
    footer.footer {
        div.sitemap {
            .sitemap-col {
                width: 100%;
            }
        }
        h4.slogan {
            margin-top: 25px;
        }
    }
}
