.fw--base     { font-weight: $font-weight-base !important; }
.fw--light      { font-weight: $font-weight-light !important }
.fw--regular    { font-weight: $font-weight-regular !important }
.fw--medium     { font-weight: $font-weight-medium !important }

.fs--small          { font-size: $font-size-small; }
.fs--medium         { font-size: $font-size-medium; }
.fs--large          { font-size: $font-size-large; }
.fs--extra-large    { font-size: $font-size-extra-large; }

.cs--pointer,
.clickable {
  cursor: pointer;
}
.cs--default { cursor: default; }

.ps--relative   { position: relative; }
.ps--fixed      { position: static; }

.uppercase { text-transform: uppercase; }
.lowercase { text-transform: lowercase; }

.opacity-0-6 { @include opacity(.6); }

.underline { text-decoration: underline; }

.nowrap { white-space: nowrap; }
.break-word { word-wrap: break-word; }

.bordered-horizontal {
    border-bottom: 1px solid $border-color;
    border-top: 1px solid $border-color;
}

.truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.va--middle { vertical-align: middle; }

.mt-xs { margin-top: $spacing-base / 4; }
.mt-sm { margin-top: $spacing-base / 2; }
.mt-md { margin-top: $spacing-base; }
.mt-lg { margin-top: $spacing-base * 2; }
.pb-xs { padding-bottom: $spacing-base / 4; }
.pb-sm { padding-bottom: $spacing-base / 2; }
.pb-md { padding-bottom: $spacing-base; }
.pb-lg { padding-bottom: $spacing-base * 2; }

.pn { padding: 0 !important; }
.pa--n { padding: 0 !important; }
.pt--n { padding-top: 0 !important; }
.pr--n { padding-right: 0 !important; }
.pb--n { padding-bottom: 0 !important; }
.pl--n { padding-left: 0 !important; }
.ph--n { padding-left: 0 !important; padding-right: 0 !important; }
.pv--n { padding-bottom: 0 !important; padding-top: 0 !important; }

.mn { margin: 0 !important; }
.ma--n { margin: 0 !important; }
.mt--n { margin-top: 0 !important; }
.mr--n { margin-right: 0 !important; }
.mb--n { margin-bottom: 0 !important; }
.ml--n { margin-left: 0 !important; }
.mh--n { margin-left: 0 !important; margin-right: 0 !important; }
.mv--n { margin-bottom: 0 !important; margin-top: 0 !important; }

.dp--block   { display: block !important; }
.dp--inline  { display: inline !important; }
.dp--inline-block  { display: inline-block !important; }
.dp--table  { display: table; width: 100%; }

.ov--hidden { overflow: hidden; }

.loading-wrapper { transition: opacity $transition-duration $transition-timing; }

.loading-wrapper--is-loading {
    @include opacity(.2);
    cursor: default;
    pointer-events: none;
}

.margin-bottom { margin-bottom: $spacing-base * 2; }
.margin-right { margin-right: $spacing-base * 2; }
.padding-bottom { padding-bottom: $spacing-base * 2; }

.no-geolocation .hidden-no-geolocation { display: none; }

.is-spinning { @include animation( spin 1s infinite linear) }

.row > .row-neutralize {
    @include make-xs-column(12);
}

.pull-right-sm {
    @media (min-width: $screen-sm-min) {
        float: right !important;
    }
}

.backdrop {
  background-color: rgba(0,0,0,0.5);
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 9999;
}

.center-both {
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

// Icon background
// --------------------------------------------------

.bg-disc {
  border-radius: 21px;
  color: #fff;
  display: inline-block;
  line-height: 35px;
  padding-top: 7px;
  text-align: center;
  width: 42px;
}

.bg-success {
  background-color: #7db239;
}

.bg-warning {
  background-color: #e56c00;
}

.bg-gray {
  background-color: #d7d7d7;
}
