.yt-wrapper {
    height: 0;
    padding: 0 0 56.1%;
    position: relative;

    iframe {
        display: block;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }

    @media (min-width: $screen-sm-min) {
        height: 360px;
        margin: 0 auto;
        padding: 0;
        width: 640px;
    }

    @media (min-width: $screen-md-min) {
        height: 480px;
        width: 854px;
    }
}

.video-preview {
    margin-bottom: $spacing-base;
    position: relative;

    @media (min-width: $screen-sm-min) {
        max-width: 50%;
    }
}

.video-preview--wide {
    max-width: none;
}

.video-preview__image {
    display: block;

    img {
        width: 100%;
    }
}

.play-button {
    background: $colorPrimary;
    border-radius: $border-radius-large;
    color: #fff;
    font-size: 36px;
    left: 50%;
    margin: -35px 0 0 -50px;
    padding: 17px 0;
    position: absolute;
    text-align: center;
    transition: opacity $transition-duration $transition-timing;
    top: 50%;
    width: 100px;
    z-index: 1;

    a:hover &,
    a:focus & {
        @include opacity(.8);
    }
}