.banner {
  margin-bottom: $spacing-base + 10;

  .cloud {
    margin-left: 8px;
    margin-right: 8px;
    margin-top: -80px;
    z-index: 2;
  }

  .cloud--above-animation {
    margin-top: -16px;
  }

  .cloud--disturbance-query {
    padding: 18px 14px 30px;

    .ws-errorbox {
      background: $swatchOrange;
      padding: 4px 8px;
      top: 4px;

      p {
        color: #fff;
      }
    }
  }
}

.banner__cloud__title.h3 {
  line-height: 1.5;
}

.banner--with-cloud:not(.banner--with-content) {
  margin-bottom: 32px;
}

.banner__image {
  background: no-repeat 50% 0;
  background-size: 140% auto;
  height: 180px;

  .section-side & {
    background-size: cover;
    height: 200px;
  }

  &.banner__image--large {
    height: 216px;
  }
}

.banner__image-absolute {
  left: 0;
  position: absolute;
  width: 100%;
}

.banner__image--wide {
  height: 240px;
  margin-left: -10px;
  margin-right: -10px;
}

.banner__content {
  background: #fff;
  border-top-right-radius: $border-radius-large;
  margin-right: $padding-small-horizontal;
  margin-top: -58px;
  padding: ($spacing-base + 10) $spacing-base 0;
  position: relative;

  .cloud + & {
    margin-top: ($spacing-base + 30) * -1;
  }

  .section-side & {
    padding-left: 0;
  }

  h1 {
    margin-top: 0;
  }

  .lead {
    font-size: 17px;
    line-height: 1.5;
    margin-bottom: 0;
  }
}

@media (min-width: $screen-xs-min) {
  .banner__image {
    background-size: 120% auto;
    height: 260px;

    .section-side & {
      background-size: cover;
      height: 224px;
    }

    &.banner__image--large {
      height: 280px;
    }
  }
}

@media (min-width: $screen-sm-min) {
  .banner {
    position: relative;

    .cloud {
      margin-left: 0;
      margin-right: 0;
      margin-top: 50px;
      padding: 30px 30px 34px;
      position: absolute;
      right: 35px;
      top: 0;
      width: 315px;
    }

    .cloud--animate {
      @include animation(fadeInRightShort ease-out .8s .8s both);
    }

    .cloud--disturbance-query {
      padding-bottom: 42px;
    }

    .cloud--search-site {
      bottom: -68px;
      top: auto;
      width: 584px;
    }
  }

  .banner--with-cloud.banner--with-content {
    margin-bottom: $spacing-base + 42px;
  }

  .banner__image {
    background-size: 100% auto;
    height: 300px;

    .section-side & {
      height: 260px;
    }

    &.banner__image--large {
      height: 320px;
    }
  }

  .banner__image--wide {
    background-size: 140% auto;
    height: 460px;
    margin-left: ($screen-sm-min - $container-sm + $grid-gutter-width) / 2 * -1;
    margin-right: ($screen-sm-min - $container-sm + $grid-gutter-width) / 2 * -1;
  }

  .banner__entrypoints {
    margin-top: -120px;
  }

  .banner__entrypoints--animate {
    @include animation(fadeInUpShort ease-out .8s 1.4s both);
  }

  .banner__content {
    bottom: 0;
    left: 0;
    margin-left: 0;
    margin-right: 0;
    padding-left: 0;
    position: absolute;
    width: 336px;

    .cloud + & {
      /*bottom: -92px;*/
    }

    h1 {
      margin-top: 0;
    }
  }
}

@media (min-width: $screen-md-min) {

  .banner--with-cloud.banner--with-content {
    margin-bottom: $spacing-base + 42px;
  }

  .banner__image {
    height: 400px;

    .section-side & {
      height: 260px;
    }

    &.banner__image--large {
      height: 320px;
    }
  }

  .banner__image--wide {
    background-size: 100% auto;
    height: 460px;
    margin-left: -40px;
    margin-right: -40px;
  }

  .banner .cloud--disturbance-query {
    padding-bottom: 56px;
  }

  .banner__entrypoints {
    margin-top: -120px;
  }

  .banner__content {
    top: auto;

    &,
    .cloud + & {
      bottom: -1px; // Nodig voor Explorer
    }
  }
}

@media (min-width: $screen-lg-min) {
  .banner__image {
    height: 486px;

    .section-side & {
      height: 286px;
    }

    &.banner__image--large {
      height: 350px;
    }
  }
}

.outage {
  .banner_image_big {
    margin: 0 -40px;
  }
}
