/**
 * Generiek
 */
caption {
    color: inherit;
    padding-bottom: 11px;
    padding-top: 0;
    text-align: left;

    h3 {
        margin-bottom: 0;
        margin-top: 0;
    }
}

@media (min-width: $screen-sm-min) {
    caption {
        .title {
            float: left;
        }

        .subtitle {
            float: left;
            padding-left: 14px;
            padding-top: 2px;
        }

        .btn {
            position: relative;
            top: -10px;
        }
    }
}

.table {
    th {
        color: $colorPrimary;
        font-size: $font-size-medium;
        font-weight: $font-weight-base;
    }

    th,
    td {
        transition: background-color $transition-duration $transition-timing;
    }

    > thead > tr > th {
        border-bottom-width: 1px;
    }

    > tbody {
        border-top: 1px solid $table-border-color;

        & + tbody {
            border-top-width: 1px;
        }

        @media (max-width: ($screen-xs-max)) {
            > tr:not(:first-child) {
                > th,
                > td {
                    border-top-width: 0;
                }
            }
        }
    }

    > thead,
    > tbody,
    > tfoot {
        > tr {
            > th.va--middle,
            > td.va--middle {
                @extend .va--middle;
            }
        }
    }
}

.panel {
    > .panel-body + .table {
        border-top-width: 0;
    }

    > .table {
        td:first-child,
        th:first-child {
            padding-left: $panel-body-padding;
        }
    }
}

@media (min-width: $screen-sm-min) {
    .table {
        > tbody > tr {
            > th,
            > td {
                padding-bottom: 16px;
                padding-top: 16px;
            }
        }
    }
}

@media (max-width: ($screen-xs-max)) {
    .panel > .table {
        th,
        td {
            padding-left: $panel-body-padding;
        }
    }
}

/**
 * .table-condensed
 */
@media (min-width: $screen-sm-min) {
    .table-condensed {
      > thead,
      > tbody,
      > tfoot {
        > tr {
          > th,
          > td {
            padding: $table-condensed-cell-padding;
          }
        }
      }
    }
}

/**
 * .table--responsive
 */

.table--responsive {
    > tbody.compact {
        border-width: 0;

        > tr {
            > th,
            > td {
                border-width: 0;
                padding: $table-condensed-cell-padding;
            }
        }

        &:first-of-type {
            > tr {
                > th,
                > td {
                    padding-top: 16px;
                }
            }
        }

        &:last-of-type {
            > tr {
                > th,
                > td {
                    padding-bottom: 16px;
                }
            }
        }
    }
}

@media (max-width: ($screen-xs-max)) {
    .table--responsive {
        display: block;

        > tbody,
        > tbody tr {
            display: block;
        }

        /* Fix for Bootstrap's Collapse */
        > tbody tr.collapse {
            display: none;
            &.in {
                display: block;
            }
        }
        /* *** */

        thead {
            display: none;
        }

        > tbody > tr {
            > th,
            > td {
                border-top-width: 0;
                display: block;

                &:before {
                    content: attr(data-th);
                    color: $colorPrimary;
                    display: block;
                    font-size: $font-size-base;
                    font-weight: $font-weight-base;
                    line-height: 1.2;
                }
            }
        }

        > tbody:not(.compact) > tr {
            > th,
            > td {
                padding-bottom: 8px;
                padding-top: 8px;

                &:first-child {
                    padding-top: 16px;
                }

                &:last-child {
                    padding-bottom: 16px;
                }
            }
        }
    }

    .panel > .table--responsive:first-child > tbody:first-of-type {
        border-top-width: 0;
    }
}
