.panel-list-group {
  display: flex;
  flex-wrap: wrap;
}

.panel-list-group-item {
  flex: 1 1 auto;
  border-top: 1px solid #ddd;
  width: 100%;

  @media (min-width: $screen-sm-min) {
    &:first-of-type:nth-last-of-type(3),
    &:first-of-type:nth-last-of-type(3) ~ & {
      width: 33.33%;
    }

    &:first-of-type:nth-last-of-type(3),
    &:first-of-type:nth-last-of-type(3) ~ &:not(:last-child) {
      border-right: 1px solid #ddd;
    }

    &:first-of-type:nth-last-of-type(4),
    &:first-of-type:nth-last-of-type(4) ~ & {
      width: 50%;
    }

    &:first-of-type:nth-last-of-type(4),
    &:first-of-type:nth-last-of-type(4) ~ &:nth-child(odd) {
      border-right: 1px solid #ddd;
    }
  }

  @media (min-width: $screen-md-min) {
    &:first-of-type:nth-last-of-type(4),
    &:first-of-type:nth-last-of-type(4) ~ & {
      width: 25%;
    }

    &:first-of-type:nth-last-of-type(4),
    &:first-of-type:nth-last-of-type(4) ~ &:not(:last-child) {
      border-right: 1px solid #ddd;
    }
  }
}
