form.webform-client-form {
  .form-group {
    @include clearfix();
  }

  .webform-component-ibanfield .ws-errorbox {
    @include make-xs-column-push(3);
    @include make-sm-column-push(0);
    margin-left: 0;

    @media (min-width: $screen-sm-min) {
      margin-left: 10px;
    }
  }

  fieldset.collapsed {
    height: inherit;
    border-width: 1px;
  }
}

/*------------------------------------*\
    $ELEMENTEN BINNEN FORMULIERERROR
\*------------------------------------*/
.ws-titlevalue {
  display: none !important;
}

.ws-po-box, .ws-po-outerbox {
  display: none !important;
}

.ws-errorbox {
    font-family: $font-family-base;
}
