hr {
  border-color: #d7d7d7;
  clear: both;
}

.panel-storingen-wrapper {
  min-height: 400px;
}

.panel-storingen {
  @include border-top-radius(3px);
  @include border-bottom-radius(3px);

  .panel-heading .row {
    margin: 0;
  }

  h2 {
    padding-left: 0;
    padding-right: 0;
  }

  dl {
    padding-left: 0;
    padding-right: 0;
  }

  dt {
    overflow: visible;
    text-overflow: clip;
    white-space: normal;
    width: 40%;
  }

  dd {
    width: 60%;

    ul {
      padding: 0;
      margin: 0;

      li {
        margin: 0;
        padding: 0;
        list-style: none;
      }
    }
  }

  dl.wider {
    @media (min-width: $screen-md-min) {
      dt {
        width: 56%;
      }

      dd {
        margin-left: 56%;
        width: 44%;
      }
    }
  }
}

.panel--outages {
  margin-bottom: 22px;
}

.outage-type {
  position: relative;
}

.outage-type--bolt {
  top: -1px;
}

.outage-type--gas {
  top: 3px;
}

@media (min-width: $screen-sm-min) {
  .outage-type--bolt {
    top: -4px;
  }
  .outage-type--gas {
    top: 0;
  }
}

.outage-icon {
  color: lighten($gray, 33%);
  font-size: 18px;
  margin-right: 6px;
}

.outage-icon--bolt {
  font-size: 22px;
  margin-right: 8px;
  position: relative;
  top: 3px;
}

.outage-icon-solved {
  color: $colorSecondary;
  font-size: 16px;
}

.outages-table-wrapper {
  min-height: 390px;
}

.outages-updated {
  font-size: $font-size-small;

  @media (min-width: $screen-sm-min) {
    position: relative;
  }
}

.outages-footnote {
  font-size: $font-size-small;

  @media (min-width: $screen-sm-min) {
    float: right;
  }
}

.outages-result-header {

  padding: 18px 20px 25px;

  label.checkbox-inline {
    white-space: nowrap;
  }

}

.outages-result-footer {
  padding: 20px;
}

.outages-message--postal {
  left: 0;
}

.outages-message--street-nr {
  float: right;
  margin-right: 27px;
  width: $tooltip-max-width;
}

.outages-message--date-from,
.outages-message--date-until {
  max-width: none;
}

.outages-result-nr {
  border-bottom: 1px solid $border-color;
  margin-bottom: ($line-height-computed / 2); // Zelfde als margin-bottom van headings
}

.outages-last-updated {
  padding-bottom: 16px;

  @media (min-width: $screen-md-min) {
    padding-bottom: 10px;
    padding-top: 12px;
    text-align: right;
  }
}

.outages-result-title {
  display: block;
  font-weight: $font-weight-base;
  margin-top: 10px;

  em {
    font-style: normal;
  }

  span {
    display: table-cell;
    height: 66px; // Hoogte van 3 regels. We willen niet dat deze heading steeds verspringt
    vertical-align: middle;
    width: 100%;
  }

  @media (min-width: $screen-xs-min) {
    span {
      height: 44px; // Hoogte van 2 regels. We willen niet dat deze heading steeds verspringt
    }
  }

  @media (min-width: $screen-sm-min) {
    span {
      /**
       * Reset de table-cell stijl
       */
      display: inline;
      height: auto;
      width: auto;
    }
  }
}

.outages-filter-types {
  @extend .list-unstyled;
  display: inline-block;
  margin-bottom: 0;

  li {
    padding-bottom: 16px;
  }

  &:first-child li {
    padding-right: 20px;
  }

  .extra {
    display: none;
  }

  @media (min-width: $screen-xs-min) {
    padding-right: 60px;

    .extra {
      display: inline;
    }
  }

  @media (min-width: $screen-sm-min) {
    padding-right: 20px;

    li {
      display: inline-block;
      padding-bottom: 0;
      padding-right: 20px;
    }
  }
}

.table-outages-wrapper {
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width: $screen-sm-min) {
    padding-left: 0;
    padding-right: 0;
  }
}

.outage-detail-wrapper {
  /**
   * padding-bottom zorgt ervoor dat een ondermarge op het laatste element
   * (.well met disclaimer tekst) in Firefox gehonoreerd blijft (bug)
   */
  /*min-height: 366px;*/
  min-height: 400px;
  padding-bottom: 1px;
}

.table-outages {
  border-bottom: 1px solid $table-border-color;

  tbody th {
    /**
     * Width of left column on small screens
     */
    width: 30%;
  }

  .col-sms {
    padding-left: 0;
    padding-right: 0;
  }

  .col-details {
    padding-left: 0;
    padding-right: 0;
  }

  .first-row {
    > th, td {
      padding-top: 16px;
    }
  }

  .last-row {
    > th, td {
      padding-bottom: 16px;
    }
  }

  > thead,
  > tbody,
  > tfoot {
    > tr {
      > th,
      > td {
        padding-bottom: 4px;
        padding-top: 4px;
      }

      > td {
        padding-top: 7px;
      }
    }
  }

  @media (min-width: $screen-sm-min) {
    tbody th {
      width: auto;
    }

    > thead,
    > tbody,
    > tfoot {
      > tr {
        > th,
        > td {
          padding-bottom: $table-cell-padding;
          padding-top: $table-cell-padding;
        }
      }
    }

    > tbody {
      > tr {
        > th,
        > td {
          padding-bottom: $table-cell-padding + 4;
          padding-top: $table-cell-padding + 4;
        }
      }
    }
  }
}

.table-outages--preview {

  .col-type {
    width: 106px;
  }
  .col-status {
    width: 110px;
  }
  .col-from {
    width: 122px;
  }
  .col-until {
    width: 122px;
  }
  .col-more {
    width: 100px;
  }

  @media (min-width: $screen-md-min) {
    .col-until {
      width: 122px;
    }
    .col-status {
      width: 186px;
    }
  }

  @media (min-width: $screen-lg-min) {
    .col-type {
      width: 120px;
    }
    .col-from {
      width: 160px;
    }
    .col-until {
      width: 180px;
    }
    .col-status {
      width: 240px;
    }
  }
}

.table-outages--overview {
  margin-bottom: 0;

  tbody:first-child tr.first-row {
    border-top: 1px solid $table-border-color;
  }

  .col-type {
    width: 106px;
  }
  .col-from {
    width: 112px;
  }
  .col-until {
    width: 112px;
  }
  .col-status {
    width: 130px;
  }
  .col-more {
    width: 100px;
  }

  @media (min-width: $screen-lg-min) {
    .col-from {
      width: 106px;
    }
    .col-until {
      width: 122px;
    }
    .col-status {
      width: 186px;
    }
  }

  @media (min-width: $screen-sm-min) {
    tbody:first-child tr.first-row {
      // Reset de border
      border-top-width: 0;
    }
  }
}

.table-outages__cell-location {
  height: 80px;
  overflow: hidden; // This is in to order to fix the long dutch place names.
}

.table-outages__outage--solved {
  background-color: #FBFBFB;
  color: #99998C;
}

.table-outages__loader {
  min-height: 400px;
  padding-top: 36px;
  text-align: center;
}

.table-outages__message {
  font-size: $font-size-small;
  padding: 20px;
}

@media (min-width: $screen-sm-min) {
  .outages-message--street-nr {
    float: none;
    left: 277px;
    margin-right: 0;
    right: auto;
    top: -58px;
  }

  .outages-message--date-from {
    left: -42px;
  }

  .outages-message--date-until {
    left: 166px;
  }

  .outages-message--date-from,
  .outages-message--date-until {
    max-width: $tooltip-max-width;
    position: absolute;
  }
}

/*------------------------------------*\
    $STREETS
\*------------------------------------*/
.outage-streets {
  margin-bottom: 6px;
  padding-top: 12px;
}

/*------------------------------------*\
    $INFO & CAUSE BLOCK
\*------------------------------------*/
.info-error-block + .cause-error-block {
  padding-top: 24px;

  @media (min-width: $screen-md-min) {
    padding-top: 0;
  }
}

/*------------------------------------*\
    $OUTAGE DETAILS
\*------------------------------------*/
.outage-details {
  margin-bottom: 17px;
}

.outage-details--extra-info {
  margin-bottom: 0;
}
