.input-group--dark {
    .input-group-addon,
    .form-control {
        border-color: $colorPrimary;
        background-color: #62165E;
        color: white;
    }

    .form-control:focus {
        border-color: $input-border-focus;
    }

    .form-control{
        @include placeholder(#BA5A92);
    }

}

.input-group--dark.has-error,
.invalid .input-group--dark {
    @include form-control-validation($colorWhite, $colorAttention, $colorAttention);

    .form-control:focus {
        box-shadow: none;
    }
}

.input-group-btn .btn {
    font-size: $font-size-base;
    line-height: $line-height-computed;
    white-space: nowrap;
}