nav.navbar {
  margin-bottom: 0;
  .navbar-nav {
    a {
      text-decoration: none;
    }
  }
}

.navbar-nav {
  padding-left: 10px;
}

.navbar-nav > li > a {
  line-height: 19px;
}

.container-top-menu {
  max-width: none;
  padding-top: 10px;
  padding-bottom: 8px;

  .webform__submit {
    display: none;
  }

  .form-control-feedback {
    font-size: 15px;
    height: 30px;
    line-height: 30px;
    right: 38px;

    .is-ie & {
      line-height: 38px;
    }
  }

  .navbar-form .input-group-addon.has-value {
    @include border-left-radius($border-radius-base);
    background-color: $colorSecondary;

    .btn,
    .btn:hover {
      color: #fff;
    }
  }

  .container-logo {
    padding-left: ($grid-gutter-width / 2); // Default padding of a grid column
    padding-right: ($grid-gutter-width / 2); // Default padding of a grid column
  }

  .top-menu ul li a {
    color: #9c9c9c;
  }

  nav.top-menu .navbar-form .form-group .search-icon {
    height: auto;
  }

  nav.main-menu {
    .container-fluid {
      .navbar-form {
        padding-left: 20px;
        padding-right: 10px;
      }
    }
  }

  .form-search .dropdown-menu li:last-child a {
    padding-left: 17px;
    padding-right: 17px;
  }
}

.container-top-menu.double-label {

  padding-top: 10px;

  nav.main-menu a {
    padding: 0 17px;
    .soft-text {
      font-size: $font-size-base;
    }

  }

  .navbar-form {
    .input-group {
      input {
        width: 120px;
      }
    }
  }

  .is-ie & .navbar-form .input-group input {
    height: 36px;
  }
}

.navbar-form {
  .search-icon {
    color: #9c9c9c;
  }

  .form-control {
    -webkit-appearance: none;
  }
}

@media (min-width: $screen-lg-min) {
  .container-top-menu.double-label {
    .navbar-form .input-group input {
      width: 180px;
    }
  }
}