div.mega-menu-container {

    &.mega-open {
        nav.mega-menu {
            max-height: 650px;

            div.menu-columns {
                max-height: 650px;
            }
        }
    }

    nav.mega-menu div.menu-columns {
        -moz-column-count: auto;
        -webkit-column-count: auto;
        column-count: auto;

        margin-left: -10px;
        margin-right: -10px;

        ul {
            padding-left: 10px;
            padding-right: 10px;
        }
    }
}