/**
 *
 * .pn {}
 * .pa--s {}
 * .pt--s {}
 * .pr--s {}
 * .pb--s {}
 * .pl--s {}
 * .ph--s {}
 * .pv--s {}
 *
 * Directions
 * a: all
 * t: top
 * r: right
 * b: bottom
 * l: left
 * h: horizontal
 * v: vertical
 *
 * Sizes:
 * xs:  extra-small
 * s:   small
 * m:   medium
 * l:   large
 * xl:  extra-larges
 *
 */
$directions: a t r b l h v;
$sizes: xs s m l xl;
$values: 2px 6px 14px 20px 32px;

$screen-xs-min: 480px !default;
$screen-sm-min: 780px !default;
$screen-md-min: 992px !default;

@mixin spaces-setup($times: 1) {

    @each $direction in $directions {
        @for $i from 0 to length($sizes) {

            $value: nth($values, $i+1) * $times;

            .p#{$direction}--#{nth($sizes, $i+1)} {
                $property: padding;
                @if $direction == a {
                    #{$property}: $value;
                } @else if $direction == t {
                    #{$property}-top: $value;
                } @else if $direction == r {
                    #{$property}-right: $value;
                } @else if $direction == b {
                    #{$property}-bottom: $value;
                } @else if $direction == l {
                    #{$property}-left: $value;
                } @else if $direction == h {
                    #{$property}-left: $value;
                    #{$property}-right: $value;
                } @else if $direction == v {
                    #{$property}-bottom: $value;
                    #{$property}-top: $value;
                }
            }

            .m#{$direction}--#{nth($sizes, $i+1)} {
                $property: margin;
                @if $direction == a {
                    #{$property}: $value;
                } @else if $direction == t {
                    #{$property}-top: $value;
                } @else if $direction == r {
                    #{$property}-right: $value;
                } @else if $direction == b {
                    #{$property}-bottom: $value;
                } @else if $direction == l {
                    #{$property}-left: $value;
                } @else if $direction == h {
                    #{$property}-left: $value;
                    #{$property}-right: $value;
                } @else if $direction == v {
                    #{$property}-bottom: $value;
                    #{$property}-top: $value;
                }
            }
        }
    }
}

@include spaces-setup();

@media (min-width: $screen-xs-min) {
    @include spaces-setup(2);
}

@media (min-width: $screen-sm-min) {
    @include spaces-setup(3);
}

@media (min-width: $screen-md-min) {
    @include spaces-setup(4);
}