.form-filter {
    position: relative;

    .form-control--width-small {
        width: 48px;
    }

    .iconed-input-wrapper {
        display: block;
    }

    .form-control--date {
        text-align: center;
    }

    .control-label {
        margin-right: 6px;
    }

    .nr-suffix {
        display: none;
    }

    .form-group--split-right {
        text-align: right;
    }

    .info-message {
        position: relative;
    }
}

.form-filter__messages {
    @include clearfix();
    position: relative;

    &.below {
        top: -14px;
    }

    &.above {
        bottom: -6px;
    }
}

.form-filter__header {
    margin-bottom: 20px;
}

.form-filter__title {
    display: inline;
}

.form-filter__reset {
    position: relative;
}

.form-filter__form {
    position: relative;
}

.form-filter__location {
    display: none;
    padding-bottom: 15px;

    .geolocation & {
        display: block;
    }
}

.form-filter__toggle {
    padding-top: 16px;
    text-align: right;
}

.form-filter__input-divider {
    display: block;
    padding: 4px 0;
}

.form-filter__submit {
    display: block;
    width: 100%;
}

.form-filter__split-wrapper {
    @include make-row();

    .control-label {
        display: block;
    }

    .form-group {
        @include make-xs-column(6);
    }
}

@media (min-width: $screen-sm-min) {
    .form-filter {
        .nr-suffix {
            display: inline;
        }

        .form-group--split-right {
            text-align: left;
        }

        .form-group {
            display: inline-block;
            padding-right: 12px;
        }

        .iconed-input-wrapper {
            display: inline-block;
        }

        .form-control {
            display: inline-block;
        }

        .form-control--postal {
            width: 100px;
        }

        .form-control--date {
            text-align: left;
            width: 130px;
        }

        .form-control--query {
            width: 194px;
        }
    }

    .form-filter__location {
        max-width: 300px;
    }

    .form-filter__more {
        padding-top: 24px;
    }

    .form-filter__input-divider {
        display: inline;
        padding: 0 4px;
    }

    .form-filter__split-wrapper {
        margin-left: 0;
        margin-right: 0;

        .control-label {
            display: inline;
        }

        .form-group {
            float: none;
            padding-left: 0;
            width: auto;
        }
    }

    .form-filter__toggle {
        position: absolute;
        right: $panel-body-padding;
        white-space: nowrap;
        top: 4px;
    }

    .form-filter__submit {
        bottom: 0;
        position: absolute;
        right: 0;
        width: 120px;
    }

    .form-filter__messages {
        &.below {
            top: auto;
        }

        &.above {
            bottom: auto;
        }
    }
}

@media (min-width: $screen-sm-min) {
    .form-filter {
        .info-message {
            position: absolute;
        }

        .info-message--relative {
            position: relative;
        }
    }

    .form-filter__messages {
        position: absolute;
        width: 100%;
    }

    .form-filter__more {
        padding-top: 16px;
    }
}

@media (min-width: $screen-md-min) {
    .form-filter__submit {
        width: 100px;
    }

    .geolocation .form-filter__location {
        display: none;
    }
}
