.children-select {
    display: block;
    margin: 0 0 $spacing-base;
    max-width: $dropdown-with;

    .btn {
        display: block;
        float: none;
        position: relative;
        text-align: left;
        z-index: $zindex-dropdown + 1;

        .icon {
            font-size: 18px;
            margin-top: -7px;
            position: absolute;
            right: 8px;
            top: 50%;
        }
    }

    .dropdown-menu {
        border-radius: $border-radius-base;
        box-shadow: 0 3px 6px -2px rgba(187, 187, 187, .75);
        font-size: $font-size-base;
        margin-left: -1px;
        margin-top: -2px;
        padding: 0 20px;
        right: 0;

        > li {

            > a {
                border-bottom: 1px solid $colorGrey;
                line-height: 1.3;
                padding: 1.1em 1em;
                padding-left: 0;
                position: relative;
                text-decoration: none;
                transition: color $transition-duration $transition-timing;
                white-space: normal;

                &:after {
                    color: $breadcrumb-color;
                    @extend .icon-arrow-light-right:before;
                    font-family: $icon-liander-font-name;
                    font-size: 12px;
                    margin-top: -7px;
                    position: absolute;
                    right: -8px;
                    top: 50%;
                    transition: color $transition-duration $transition-timing;
                }

                &:hover,
                &:focus,
                &.active {
                    background-color: transparent;
                    color: $colorSecondary;
                }
            }

            &:last-child > a {
                border-bottom-width: 0;
            }
        }
    }

    .nav-side & {
        margin-right: $spacing-base;
        max-width: none;
    }
}
