.btn-file {
  overflow: hidden;
  position: relative;

  input[type=file] {
    @include opacity(0);
    background: red;
    cursor: inherit;
    display: block;
    font-size: 100px;
    min-height: 100%;
    min-width: 100%;
    outline: none;
    position: absolute;
    right: 0;
    text-align: right;
    top: 0;
  }
}

.liander-file-upload {
  span.input-group-btn{
    .btn {
      width: 130px;
    }
  }
}

span.remove-uploaded {
  display:none;
}
