.nav-side {
    padding-top: $spacing-base;
}

.nav-side__header {
    color: $colorSecondary;
    font-size: $font-size-h2;

    a {
        text-decoration: none;
    }
}

.nav-side__list {
    border-top: 1px solid $colorGrey;
    padding-left: 0;
    list-style-type: none;

    li {
        display: block;
    }
}

.nav-side__anchor {
    border-bottom: 1px solid $colorGrey;
    color: $colorGreyDark;
    display: block;
    font-size: $font-size-base;
    line-height: 1.3;
    padding: 1.1em 2.2em;
    padding-left: 0;
    position: relative;
    text-decoration: none;
    transition: color $transition-duration $transition-timing;

    &.active {
        color: $colorSecondary;
    }

    .icon {
        margin-top: -8px;
        position: absolute;
        right: 15px;
        top: 50%;
    }
}

.nav-side__list {
  li {
    &.active > .nav-side__anchor {
      color: $colorSecondary;
    }
  }
}

.nav-side__sub-list {
    padding-left: 0;
}

.nav-side__sub-anchor {
    background-color: $colorGreyLighter;
    border-bottom: 1px solid $colorGrey;
    color: $colorGreyDark;
    display: block;
    padding: 1em 16px;
    text-decoration: none;

    &.active {
        color: $colorSecondary;
    }
}
