.alert-error {
  @include alert-variant($alert-error-bg, $alert-error-border, $alert-error-text);
}

.alert--small {
    margin-bottom: $alert-padding / 2;
    padding-bottom: $alert-padding / 2;
    padding-top: $alert-padding / 2;
}

.alert--normal-content {
    > p + p,
    > p + ul {
        margin-top: $spacing-base - 5;
    }
}

.alert-icon {
    position: relative;
    padding-left: 70px;

    > .icon {
        left: $spacing-base * 2;
        position: absolute;
        top: $spacing-base + 3;

        &.icon-lg {
            left: $spacing-base;
            top: $spacing-base;
        }
    }

    .h2,
    h2 {
        margin-top: 5px;
    }
}

.alert--calamity {
    margin-bottom: 14px;
    margin-top: 14px;
    padding: $alert-padding;
    padding-top: 66px;

    color: #fff;

    a {
        text-decoration: none;
        color: #fff;

        &:hover,
        &:focus {
            color: #fff;
        }
    }

    > .icon.icon-lg {
        top: 16px;
    }

    .title {
        margin: 0;
    }

    .alert-close {
        padding: 12px;
        position: absolute;
        right: 4px;
        top: 4px;
        cursor: pointer;
    }

    @media (min-width: $screen-sm-min) {
        padding: $alert-padding;
        padding-left: 70px;
        padding-right: 54px;

        > .icon.icon-lg {
            top: 13px;
        }

        .alert-close {
            margin-top: -23px;
            top: 50%;
        }
    }
}
