.modal-open {
    /**
     * Bootstrap voorkomt dat je kunt scrollen als een modal openstaat. We
     * resetten dat hier omdat het ook zorgt voor verspringen (scrollbar) wordt
     * wel/niet getoond
     */
    overflow-y: visible;
}

.modal {
    overflow-y: auto;
    z-index: 999999;
}

.modal-backdrop {
  z-index: 99999;
}

.modal-dialog {
    margin-left: auto;
    margin-right: auto;
    padding-left: 4px;
    padding-right: 4px;
    max-width: 320px;

    @media (min-width: $screen-sm-min) {
        max-width: none;
    }

    @media (min-width: $screen-md-min) {
        width: $modal-lg;
    }
}

.modal-header {
    padding-left: 0;
    padding-right: 0;
    margin-left: $spacing-base;
    margin-right: $spacing-base;
    margin-top: 8px;

    .close {
        margin-top: 6px;
    }
}

.modal--video {
    .close {
        bottom: 12px;
        position: absolute;
        right: $spacing-base;
        z-index: 1;
    }

    .modal-body {
        padding-bottom: 50px;
    }

    @media (min-width: $screen-sm-min) {
        .close {
            bottom: auto;
            top: 17px;
        }

        .modal-body {
            padding-bottom: $modal-inner-padding + 6;
        }
    }
}

#spanElectraLiander,
#spanElectraOther,
#spanGasLiander,
#spanGasOther {
	color: $colorGreyDark;
	font-size: 15px;
    text-align: center;
}
