/*------------------------------------*\
    $ALGEMEEN
\*------------------------------------*/
label {
    font-weight: normal;
}

input[type=search] {
    // Set font-size: 16px to prevent iOS from zooming in, when focussed.
    font-size: 16px;
}

input[type=number] {
    -moz-appearance: textfield;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=radio],
input[type=checkbox] {
    /**
     * Overrides.css (Drupal) zegt dat alle inputvelden een max-width hebben
     * Dat gaat echter niet goed nie voor dit soort velden
     */
    max-width: none;
}

input[type=radio] {
  margin-top: 1px;
}

.form-radios {
  padding-top: 7px;
}

input[type=file].form-control.user-success {
    border: none;
}

input[type=file].form-control.user-error {
    border: 1px solid #E56C00;
}


/**
 * Pijltjes die webshim toevoegt om een input[type=number] na te bootsen
 * in browser die dat niet ondersteunen
 */
.number-input-buttons {
    display: none;
}

/*------------------------------------*\
    $BOOTSTRAP
\*------------------------------------*/
.form-control.input-lg {
    border-radius: $border-radius-base;
}

.has-error,
.invalid  {
    @include form-control-validation($colorAttention, $colorAttention, $colorAttention);
}

.has-feedback {
    .form-control {
        padding-right: 30px;
    }

    .input-lg {
        padding-right: 45px;
    }
}

.form-control {
    font-size: $font-size-small;
    @include box-shadow(none);
    background: #fff;
    padding-left: $padding-base-horizontal - 4;
    padding-right: $padding-base-horizontal - 4;
    width: 100%;

    &[disabled] {
        background: $input-bg;
        cursor: default;
    }

    // De wissen-knop (kruisje)
    &::-ms-clear {
        display: none;
    }

    .has-error &:focus,
    .invalid &:focus,
    &:focus {
        @include box-shadow(none);
    }

    .has-error &,
    .invalid & {
        &:focus {
            /**
             * We hebben dit nodig om de stijl te overschrijven die de Bootstrap
             * mixin 'form-control-validation' genereert
             */
            border-color: $colorAttention;
        }

        &[disabled] {
            border-color: $input-border;
        }
    }

}

.form-control--unbordered {
    &,
    &:focus {
        border-color: transparent;
    }
}

.is-ie-9,
.is-ie-10 {
    .radio input[type="radio"],
    .radio-inline input[type="radio"],
    .checkbox input[type="checkbox"],
    .checkbox-inline input[type="checkbox"] {
        margin-top: 4px;
    }
}

/*------------------------------------*\
    $DONKERE VELDEN
\*------------------------------------*/
.input-group-addon--dark,
.form-control--dark,
.form-control--dark[disabled] {
    background-color: #62165E;
    background-color: rgba(0, 0, 0, .24);
    border-color: transparent;
}

.form-control--dark {
    color: #fff;

    @include placeholder(#BA5A92);
}

.input-group-addon--dark {
    color: $colorWhite;
}



div.error, table tr.error {
    background-color: transparent;
    color: $swatchGrey;
}

.webform__submit {
    padding-top: 8px;
    text-align: right;

    .trigger8 & {
        text-align: left;
    }
}

form .form-submit
{

    width: 100%;

    @media (min-width: $screen-sm-min) {
        width: auto;
    }

}

div.error, table tr.error {
    background-color: transparent;
    color: $swatchGrey;
}

.form-group {

    a.label {
        border-radius: 50%;
        cursor: pointer;
        font-size: 16px;
        height: 24px;
        margin-left: 8px;
        padding: 5px 0 0 0;
        position: absolute;
        text-align: center;
        right: 12px;
        top: 8px;
        width: 24px;
    }

    &.has-error {
        label {
            color: $text-color;
        }
    }
}

.form-control--width-small {

    width: 54px;

    &.input-lg {
        width: 86px;
    }
}

.form-control--width-medium {
    width: 160px;
}

.form-control--width-large {
    width: 200px;
}

.iconed-input-wrapper {
    display: inline-block;
    position: relative;
}

.form-control--icon-right {
    padding-right: 32px;
}

.form-submit-right {
    padding-top: $padding-base-vertical + 4;

    @media (min-width: $screen-sm-min) {
        text-align: right;
    }
}

form .input-icon { // Deze selector moet specifieker worden dan '.icon'
    color: $colorPrimary;
    font-size: $font-size-medium;
    margin-top: (($font-size-medium) / 2) * -1;
    position: absolute;
    right: 10px;
    top: 50%;
}
