/* ==========================================================================

    Part: Buttons

    Button styles zijn gebaseerd op Bootstrap-defaults.

    Is:
      Een link/form-button.

    Context:
      Kan gebruikt worden in verschillende contexten.
      - Overal waar ook gewone tekst gebruikt kan worden (inline)
      - Als submit-button van formulieren
      - Als button in formulieren/widgets.

    Contains:
      - een tekst-label
      - (optioneel) een icoon, voor de tekst of na de tekst

    States:
      - Default/Primary - Voor de belangrijkste call-to-action's
      - Secondary       - Voor andere buttons, bij gebruik naast een primary-button
      - Alternate       - Voor gebruik in boxes/kaders met een andere achtergrond
      - Contrast        - Voor gebruik op fotografie

   ========================================================================== */

/*------------------------------------*\
    $BOOTSTRAP OVERRIDE
\*------------------------------------*/
.btn {
  @include font-smoothing(off);
  font-size: $font-size-base;
  text-decoration: none;
  white-space: normal;
  transition: all .2s ease;

  &:hover,
  &:focus {
    text-decoration: none;
  }

  &.active,
  &:active {
    background-image: none;
    box-shadow: none;
  }

  &,
  &:active,
  &.active {
    &:focus {
      outline: 0;
    }
  }

  .icon {
    vertical-align: middle;
  }

  .icon-md {
    top: -1px;
  }
}

.btn-lg {
  border-radius: $border-radius-base;
  font-size: $font-size-large;
}

.btn-sm,
.btn-xs {
  border-radius: $border-radius-base;
  font-size: 14px;
  font-weight: $font-weight-base;
}

.btn--capitals {
  font-size: $font-size-base;
  font-weight: $font-weight-base;
}

.btn--submit {
  min-width: 168px;
  padding-right: 36px;
  position: relative;
  text-align: left;

  .icon {
    margin-top: -7px;
    position: absolute;
    right: $padding-base-horizontal;
    top: 50%;
  }

  .icon-md {
    margin-top: -11px;
  }
}

/*------------------------------------*\
    $LARGE BUTTON WITH FIXED WITH
\*------------------------------------*/
.btn-lg--fixed {
  max-width: 308px;
  min-width: 100%;

  @media (min-width: $screen-sm-min) {
    margin-bottom: 16px;
    min-width: 246px;
  }
}

.btn__sub {
  display: block !important;
  font-size: 70%;
}

/*------------------------------------*\
    $PRIMARY
\*------------------------------------*/
.btn-primary {
  color: $colorWhite;

  &:hover,
  &:focus {
    background-color: darken($colorAttention, 6%);
    border-color: darken($colorAttention, 6%);
  }

  &.active,
  &:active {
    background-color: darken($colorAttention, 12%);
    border-color: darken($colorAttention, 12%);
  }

  &[disabled] {
    background-color: $colorAttention;
  }
}

/*------------------------------------*\
    $ALTERNATE
\*------------------------------------*/
.btn-alternate {
  background-color: $colorAttentionAlt;
  border-color: $colorAttentionAlt;
  color: $colorWhite;

  &:hover,
  &:focus {
    background-color: darken($colorAttentionAlt, 6%);
    border-color: darken($colorAttentionAlt, 6%);
    color: $colorWhite;
  }

  &.active,
  &:active {
    background-color: darken($colorAttentionAlt, 12%);
    border-color: darken($colorAttentionAlt, 12%);
  }

  &[disabled] {
    background-color: $colorAttentionAlt;
  }
}

/*------------------------------------*\
    $PRIMARY-ALTERNATE
\*------------------------------------*/
.btn-primary-alternate {
  background-color: darken($colorPrimary, 6%);
  border-color: darken($colorPrimary, 6%);
  color: $colorWhite;

  &:hover,
  &:focus,
  &:active,
  &.active {
    background-color: $colorWhite;
    border-color: $colorWhite;
    color: $colorPrimary;

    .btn-subtitle {
      color: $colorGreyDark;
    }
  }

  &[disabled] {
    background-color: darken($colorPrimary, 6%);
  }
}

/*------------------------------------*\
    $SECONDARY
\*------------------------------------*/
.btn-secondary {
  background-color: $colorSecondary;
  border-color: $colorSecondary;
  color: $colorWhite;

  &:hover,
  &:focus {
    background-color: darken($colorSecondary, 6%);
    border-color: darken($colorSecondary, 6%);
    color: $colorWhite;
  }

  &.active,
  &:active {
    background-color: darken($colorSecondary, 12%);
    border-color: darken($colorSecondary, 12%);
  }

  &[disabled] {
    background-color: $colorSecondary;
  }
}

/*------------------------------------*\
    $CONTRAST
\*------------------------------------*/
.btn-contrast {
  background-color: $colorWhite;
  border-color: $colorWhite;
  color: $colorAttention;

  &:hover,
  &:focus {
    background-color: darken($colorAttention, 6%);
    border-color: darken($colorAttention, 6%);
    color: $colorWhite;

    .btn-subtitle {
      color: $colorWhite;
    }
  }

  &.active,
  &:active {
    background-color: darken($colorAttention, 12%);
    border-color: darken($colorAttention, 12%);
    color: $colorWhite;

    .btn-subtitle {
      color: $colorWhite;
    }
  }
}

.btn-contrast-blue {
  background-color: $colorWhite;
  border-color: $colorWhite;
  color: $colorSecondary;

  &:hover,
  &:focus {
    background-color: darken($colorSecondary, 6%);
    border-color: darken($colorSecondary, 6%);
    color: $colorWhite;

    .btn-subtitle {
      color: $colorWhite;
    }
  }

  &.active,
  &:active {
    background-color: darken($colorSecondary, 12%);
    border-color: darken($colorSecondary, 12%);
    color: $colorWhite;

    .btn-subtitle {
      color: $colorWhite;
    }
  }
}

/*------------------------------------*\
    $LINK-LIKE BUTTONS
\*------------------------------------*/
.btn-link {
  padding-left: 0;
  padding-right: 0;

  &.active,
  &:active,
  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.link-primary {
  color: $colorAttention;

  &.active,
  &:active,
  &:hover,
  &:focus {
    color: $colorAttention;
  }
}

.link-secondary {
  color: $colorSecondary;

  &.active,
  &:active,
  &:hover,
  &:focus {
    color: $colorSecondary;
  }
}

.link-muted {
  color: $colorGreyDark;

  &.active,
  &:active,
  &:hover,
  &:focus {
    color: $colorGreyDark;
  }
}

.link-white {
  color: #fff;

  &.active,
  &:active,
  &:hover,
  &:focus {
    color: #fff;
  }
}

/*------------------------------------*\
    $(MOBILE) MENU BUTTONS
\*------------------------------------*/
.btn-mainmenu {
  background-color: transparent;
  border-color: transparent;
  color: $colorSecondary;
  font-size: 40px;
  padding: 0px 12px;

  &.active,
  &:active,
  &:hover,
  &:focus {
    //TODO: focus-state?!
    background-color: transparent;
    border-color: transparent;
    color: $colorSecondary;
  }
}

/*------------------------------------*\
    $INPUT-GROUP BUTTON
\*------------------------------------*/
/**
    Make a Input-Group-button look like an inline icon:
*/
.input-group-btn-ininput {
  position: relative;

  .form-control {
    padding-right: 40px;
  }

  .btn-ininput {
    position: absolute;
    right: 0;
    top: 0;
    //z-index: 100;
  }
}

/*------------------------------------*\
    $BUTTON IN INPUT
\*------------------------------------*/
.btn-ininput {
  color: $colorSecondary;
  border-color: transparent;
  background-color: transparent;
}

/*------------------------------------*\
    $BUTTON WITH ICON
\*------------------------------------*/
.icon-after,
.icon-before {
  padding-left: 26px;
  padding-right: 26px;

  span {
    &:before {
      position: absolute;
      left: -19px;
      top: 2px;
      font-size: 110%;
    }
  }

  &.btn-lg {
    padding-left: 44px;
    padding-right: 44px;

    span:before {
      top: -5px;
      left: -32px;
      font-size: 100%;
    }
  }
}

.icon-after {
  direction: rtl;

  span {
    &:before {
      left: auto;
      right: -19px;
    }
  }

  &.btn-lg {
    span:before {
      left: auto;
      right: -32px;
    }
  }
}

/*------------------------------------*\
    BUTTON SUBTITLE
\*------------------------------------*/
.btn-subtitle {
  display: block;
  font-size: 14px;
  color: $colorGreyDark;
}

.btn-primary .btn-subtitle,
.btn-success .btn-subtitle,
.btn-info .btn-subtitle,
.btn-warning .btn-subtitle,
.btn-danger .btn-subtitle,
.btn-alternate .btn-subtitle,
.btn-primary-alternate .btn-subtitle {
  color: $colorWhite;
}

/*------------------------------------*\
    TRIGGER 8 & 14
\*------------------------------------*/
.btn-lg-wrapper {
  padding: 18px 0 0;

  .btn {
    margin-right: $spacing-base;
  }
}

.link-wrapper {
  margin-top: 28px;
}

.trigger8-ok A.btn {
  margin-left: -45px;
  color: white;
  padding-top: 80px;
}

/*------------------------------------*\
    $BUTTON FOR FILTER OPTIONS
\*------------------------------------*/
.btn-filter {
  background-color: $colorWhite;
  border-color: $colorGrey;
  color: $colorGreyDark;

  &:hover,
  &:focus {
    background-color: darken($colorPrimary, 6%);
    border-color: darken($colorPrimary, 6%);
    color: $colorWhite;
  }

  &.active,
  &:active {
    background-color: darken($colorPrimary, 12%);
    border-color: darken($colorPrimary, 12%);
    color: $colorWhite;
  }
}

/*------------------------------------*\
    $GEO BUTTON FOR DESKTOP
\*------------------------------------*/
.btn-geo-desktop {
  font-size: $font-size-small;
  left: -2px;
  position: relative;
  top: -2px;

  .icon {
    margin-right: 5px;
    top: -2px;
  }
}

/*------------------------------------*\
    Survey Button (Feedback)
\*------------------------------------*/
.btn-survey {
  bottom: 0;
  border-radius: 0 4px 0 0;
  left: 0;
  opacity: 0;
  position: fixed;
  z-index: 999;
  overflow: hidden;

  .survey-text-wrapper {
    display: inline-block;
    @include transition(width 0.8s ease-out);
    width: 0;
  }

  .survey-text-long {
    display: none;
    white-space: nowrap;
  }
}

@media (min-width: $screen-sm-min) {
  .btn-survey.hover .survey-text-short,
  .btn-survey:hover .survey-text-short {
    display: none;
  }

  .btn-survey.hover .survey-text-long,
  .btn-survey:hover .survey-text-long {
    display: inline-block;
  }
}

.btn-fixed {
  padding: 10px;
  width: 210px;
  height: 47px;

  > img {
    max-height: 25px;
    max-width: 180px;
  }

}
