footer.footer {
  margin-top: 64px;
  font-size: $font-size-small;

  .footer-links {
    .footer-logo-slogan {
      padding: 0;
    }

    .col {
      padding-left: ($grid-gutter-width / 2);
      padding-right: ($grid-gutter-width / 2);
    }
  }

  .bottom-left {
    color: #929292;
    padding: 8px 0;

    span {
        padding: 1px 15px 0 15px;
        border-right: 1px solid #929292;

        &:first-child {
          padding-left: 0;
        }
        &.last {
            border-right: 0;
        }
        a {
            color: #929292;
            text-decoration: none;
            &:hover {
                border-bottom: solid 1px #929292;
                display: inline;
                padding-bottom: 3px;
            }
        }
    }
  }

  .region-footer-social ul.social-media-links {
    margin-left: -8px;
    margin-right: -8px;
    text-align: left;
  }


  .slogan {
    font-size: $font-size-large;
  }

  @media (min-width: $screen-sm-min) {
    h4.slogan {
      margin-top: 0;
    }
  }

  @media (min-width: $screen-md-min) {
    .footer-links .footer-logo-slogan {
      padding-left: 0;
      padding-right: 0;
    }

    .region-footer-social ul.social-media-links {
      text-align: right;
    }
  }
}