@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {

    .container-header {
        width: 100%;
    }

    .container-top-menu {

        &.double-label {
            nav.main-menu a {
                padding: 0 17px;
            }
            .top-menu {
                margin-right: 6px;
                form.navbar-form {
                    margin-top: 18px;
                }
                ul.nav {
                    margin-top: 32px;
                }
            }

            .top-menu {
                margin-right: 6px;
                form.navbar-form {
                    margin-top: 22px;
                }
                ul.nav {
                    margin-top: 32px;
                }
            }
        }

        &.single-label {
            .main-menu.navbar ul > li {
                &:first-child a {
                    padding: 0;
                    border: none;
                }
                &:last-child a {
                    border: none;
                }
                a {
                    border: none;
                }
            }

            .container-top-menu {
                padding-right: 0;
            }

            .top-menu {
                .container-fluid {
                    padding-right: 0;
                }
                form.navbar-form {
                    margin-top: 16px;
                }
                ul.nav {
                    margin-top: 16px;
                }
            }

        }

        .container-logo {
            img {
                width: 110px;
                height: 51px;
            }
        }

        .container-top-menu-links {
            padding: 0;
            .container-fluid {
                padding: 0;
            }

        }


        .main-menu.navbar{
            .nav {
                width: 100%;
            }

            .nav > li {
                display: table-cell;
                width: 1%;
                float: none;
                a {
                    white-space: nowrap;
                    padding: 0 0 0 17px;
                }
                &:first-child a {
                    border-left: 1px solid $swatchGreyLight;
                }
                &:last-child a {
                    border-right: 1px solid $swatchGreyLight;
                }
            }

        }
    }
    .navbar-is-fixed-top .sticky-menu,
    .navbar-is-fixed-bottom .sticky-menu {
        .container-top-menu {
            &.double-label {
                padding-top: 2px;
                padding-bottom: 12px;
            }
        }
    }
}
