.container-header {
    max-width: 1280px;
    margin: 0 auto;
    background-color: #fff;
    &.sticky-menu {
      max-width: 100%;
    }
}

/**
 * Sticky Menu.
 *
 * The stickyness of the menu is complicated:
 *  * user can configure stickyness to be on/off for all responsive version
 *  * and specifically disable the desktop stickyness
 *  * the :not(.navbar-no-desktop) and @media queries below handle this
 */
.navbar-is-fixed-top:not(.navbar-no-desktop) .sticky-menu {
    position: fixed;
    top: 0;
    @include box-shadow(0 2px 3px -2px $swatchGreyMediumDark);
}

.navbar-is-fixed-bottom:not(.navbar-no-desktop) .sticky-menu {
    position: fixed;
    bottom: 0;
    @include box-shadow(0 2px 3px -2px $swatchGreyMediumDark);
}

.navbar-is-static-top .sticky-styleable {
    background-color: #fff;
}

.navbar-is-fixed-top:not(.navbar-no-desktop) .sticky-menu,
.navbar-is-fixed-bottom:not(.navbar-no-desktop) .sticky-menu {
    width: 100%;
    background-color: #fff;
    z-index: 10000;
    .container-top-menu {
        .main-menu {
            margin-top: 20px;
        }

        &.double-label {
            padding-bottom: 0;
            .main-menu {
                margin-top: 8px;
            }
        }

        img.logo {
            width: 85px;
            height: 40px;
        }
        .container-fluid {
            padding-top: 7px;
        }
    }
}

/* Now repeat everything to make sure it's on for tablet+mobile */
@media (max-width: $screen-desktop) {
    .navbar-is-fixed-top .sticky-menu {
        position: fixed;
        top: 0;
        @include box-shadow(0 2px 3px -2px $swatchGreyMediumDark);
    }

    .navbar-is-fixed-bottom .sticky-menu {
        position: fixed;
        bottom: 0;
        @include box-shadow(0 2px 3px -2px $swatchGreyMediumDark);
    }

    .navbar-is-static-top .sticky-styleable {
        background-color: #fff;
    }

    .navbar-is-fixed-top .sticky-menu,
    .navbar-is-fixed-bottom .sticky-menu {
        width: 100%;
        background-color: #fff;
        z-index: 10000;
        .container-top-menu {
            .main-menu {
                margin-top: 20px;
            }

            &.double-label {
                padding-bottom: 0;
                .main-menu {
                    margin-top: 8px;
                }
            }

            img.logo {
                width: 85px;
                height: 40px;
            }
            .container-fluid {
                padding-top: 7px;
            }
        }
    }
}

/**
 * Top menu (single labels)
 */
.container-top-menu {
    padding-top: 6px;
    max-width: 970px;

    .container-logo {
        padding: 16px;
    }
    img.logo {
        transition: all 0.2s linear;
        width: 130px;
        height: 60px;
    }

    nav.navbar {
        margin-bottom: 0;
    }

    nav.top-menu {
        min-height: 55px;
        li.dropdown {
          a.dropdown-toggle {
              border-left: 1px solid #fff;
              border-top: 1px solid #fff;
              border-right: 1px solid #fff;
              margin-top: -1px;
          }
          &.open {
            a.dropdown-toggle {
                background: #fff;
                border-left: 1px solid $dropdown-border;
                border-top: 1px solid $swatchGreyLight;
                border-right: 1px solid $dropdown-border;
                border-radius: 4px 4px 0 0;
            }
            ul.dropdown-menu {
                width: 100%;
                min-width: inherit;
                border-top: none;
                overflow: hidden;
                @include box-shadow(0 2px 3px -2px $swatchGreyMediumDark);
                li a {
                    padding: 4px 4px 4px 8px;
                    &:hover {
                        background-color: $swatchBlue;
                    }
                }
            }
          }
        }
        a {
            font-size: 13px;
            color: $swatchGreyMediumDark;
            padding: 10px;
            &:hover {
                color: $swatchGreyDark;
            }
        }
        .navbar-form {
            padding-right: 0;
            .form-group {
                margin-top: 4px;
                input {
                    height: 30px;
                    width: 111px;
                }
                .search-icon {
                    height: 30px;
                    font-size: 13px;
                }
            }
        }
    }

    nav.main-menu {
        min-height: 45px;
        text-transform: uppercase;
        a {
            @include fontBold();
            font-size: $font-size-base;
            color: $swatchGreyDark;
            padding: 0 10px;
            border-right: 1px solid $swatchGreyLight;
            &:hover {
                color: $swatchPurple;
            }
            &:active, &.active {
                color: $swatchBlue;
            }
            .soft-text {
                @include fontBook();
                font-size: $font-size-base;
                display: block;
                text-transform: none;
            }
        }
        ul li:last-child a {
            padding: 0 0 0 14px;
            border-right: none;
        }
        .container-fluid {
            padding-right: 0;
            .navbar-form {
                padding-right: 10px;
            }

        }
    }

    .navbar-form {
        margin: 0;
        input {
            box-shadow: none;
            border-color: #ccc;
            color: $swatchGreyMediumDark;
        }
        .btn {
            padding: 0;
            margin: 0;
            border: 0;
            line-height: 14px;
            background: none;
        }
        .search-icon {
            background-color: transparent;
            border-left: none;
            color: $swatchGreyMediumDark;
        }
        .form-control {
            border-right: none;
        }
    }
}


/**
 * Top menu (double labels)
 */
.container-top-menu.double-label {
    padding-bottom: 9px;

    nav.top-menu {
        min-height: 30px;
        ul li {
            a {
                padding: 0 8px;
            }
            &:last-child {
                a {
                    padding: 0 0 0 8px;
                }
            }
        }
    }

    nav.main-menu a {
        padding: 0 15px;
    }

    .navbar-form .input-group {
        margin-top: 4px;
        left: 9px;
        input {
            width: 110px;
            height: 31px;
        }
    }
}

.container-top-menu.double-label {
    padding: 0 15px;

    nav.main-menu a {
        padding: 0 17px;
    }
}

.container-top-menu {
    nav.main-menu {
        .container-fluid {
            .navbar-form {
                padding-right: 10px;
                padding-left: 20px;
            }
        }
    }
}
