@media (max-width: $screen-xs-max) {

    .mobile-header {
        padding: 15px 19px 0 19px;
        z-index: 100;
        background-color: #fff;
        width: 100%;

        &.container-fluid {
            padding-left: 10px;
            padding-right: 10px;
        }

        .mobile-menu-link .fa {
            position: relative;
            color: #008cbe;
        }

        .main-level,
        .sub-level,
        .mobile-search {
            max-height: 0;
            overflow: hidden;
            transition: max-height 0.8s ease-in-out;
        }

        &.mobile-menu-open {
            .main-level,
            .sub-level {
                max-height: 500px;
            }
        }

        &.mobile-search-open {
            .mobile-search {
                max-height: 500px;
            }
            .tri-up {
                display: block;
                bottom: -15px;
            }
        }

        .mobile-header-container {
            img.logo {
                width: 65px;
                height: 30px;
            }
            .mobile-top-item {
                padding: 0;
                margin: 0 5px;

                .mobile-menu-link {
                    border: 1px solid #929292;
                    border-radius: 6px;
                    box-shadow: 1px 1px 2px 0px rgba(210, 210, 210, 0.75);
                    display: inline-block;
                    padding: 10px 11px 5px;
                }

                .mobile-menu-link:active {
                    box-shadow: inset 1px 1px 2px 0px rgba(210, 210, 210, 0.75);
                }

                .mobile-search-link {
                  display: inline-block;
                  padding: 10px 11px 5px;
                }
            }
        }

        .mobile-sticky {
            padding-bottom: 14px;
            a {
                @include fontBold();
                font-size: $font-size-base;
                text-transform: uppercase;
                span {
                    font-size: $font-size-large;
                }
            }
        }

        .mobile-search, .main-level, .sub-level {
            @include box-shadow(0 2px 3px -2px $swatchGreyMediumDark);
        }
        .row.nav.sub-level {
            ul:nth-child(2) {
                padding-left: 15px;
                li {
                    padding-left: 0;
                }
            }
            ul:nth-child(3) {
                padding-left: 35px;
                li {
                    padding-left: 0;
                }
            }
            ul:nth-child(4) {
                padding-left: 35px;
                li {
                    padding-left: 0;
                }
            }
            span.fa {
                font-size: 20px;
            }
        }
        .mobile-search {
            border-top: 1px solid $swatchGreyLight;
            .navbar-form {
                margin: 0;
                min-height: 60px;
                border: 0;
                .form-group div:first-child {
                    padding: 0 5px 0 0;
                }
                .form-group div:last-child {
                    padding: 0 0 0 5px;
                }
                input[type="search"] {
                    border-radius: 4px;
                    background-color: $swatchGreyLighter;
                    font-size: $font-size-base - 1;
                    @include fontLight();
                    height: 40px;
                }
                button {
                    @include fontBold();
                    color: #fff;
                    background-color: $swatchPurple;
                    border: 1px solid $swatchPurple;
                    width: 40px;
                    height: 40px;
                }
            }
        }

        .row.nav {
            ul:last-child {
                li:last-child {
                    border-bottom: 0;
                }
            }
            ul {
                list-style: none;
                margin: 0;
                padding: 0;
                li {
                    padding: 18px 15px;
                    font-size: 15px;
                    border-bottom: 1px solid $swatchGreyLight;
                    &.back {
                        span {
                            padding: 0 10px 0 0;
                        }
                        @include fontBold();
                        padding: 18px 15px;
                        margin-left: 0;
                        a {
                            color: $swatchBlue;
                        }
                    }
                    &.header {
                        @include fontBold();
                        padding: 18px 15px;
                        margin-left: 0;
                        color: $swatchPurple;
                        text-transform: uppercase;
                    }
                    &.item {
                        padding-left: 30px;
                    }
                    &.active {
                        a {
                            color: $swatchPurple;
                        }
                    }
                    a {
                        color: $swatchGreyDark;
                        width: 100%;
                        display: block;

                        i.fa-angle-right {
                            margin-top: 2px;
                            font-size: 20px;
                        }
                    }
                    span {
                        &.pull-right {
                            padding-right: 15px;
                        }
                        color: $swatchGreyMediumDark;
                    }
                }
                &.mobile-main {
                    @include fontBold();
                    li {
                        a {
                            text-transform: uppercase;
                        }
                    }
                }
                &.mobile-top {
                  @include fontBook();
                    padding: 10px 0;
                    li {
                        border: none;
                        padding: 7px 15px;
                    }
                    .language-choice-options {
                        display: none;
                        max-height: 0;
                        transition: max-height 0.4s ease-in-out;
                        overflow: hidden;
                        &.open {
                            display:block;
                            max-height: 65px;
                        }
                        li:last-child {
                            padding-bottom: 0;
                        }
                    }
                }
            }
        }
    }

    .mobile-back {
        @include gradient-vertical(
            $swatchGreyLight,
            $swatchGreyMediumLight,
            0%,
            8%
        );
        padding: 2px 14px;
        font-size: $font-size-base + 1;
        color: $swatchBlue;
        span {
            padding: 15px;
        }
        span.fa-angle-left {
            color: $swatchGreyDark;
            font-size: 20px;
        }
    }
}
