@mixin fontNoPrefix($family, $style) {
  family: $family, Verdana, sans-serif;
  style: $style;
}

@mixin fontPrefixed($family, $style) {
  font-family: $family, Verdana, sans-serif;
  font-style: $style;
}

@mixin getFont($fontFamily, $style, $prefixed) {
  @if $prefixed == "no" {
    @include fontNoPrefix($fontFamily, $style);
  } @else {
    @include fontPrefixed($fontFamily, $style);
  }
}

@mixin fontLight($fontPrefix:yes) {
  @include getFont('Proxima Nova Light', normal, $fontPrefix);
}

@mixin fontBook($fontPrefix:yes) {
  @include getFont('Proxima Nova Regular', normal, $fontPrefix);
}

@mixin fontBookItalic($fontPrefix:yes) {
  @include getFont('Proxima Nova Regular', italic, $fontPrefix);
}

@mixin fontMedium($fontPrefix:yes) {
  @include getFont('Proxima Nova SemiBold', normal, $fontPrefix);
}

@mixin fontMediumItalic($fontPrefix:yes) {
  @include getFont('Proxima Nova SemiBold', italic, $fontPrefix);
}

@mixin fontBold($fontPrefix:yes) {
  @include getFont('Proxima Nova Bold', normal, $fontPrefix);
}
