.well {
    @include box-shadow(none);
    border: 0;
    border-top-left-radius: 0;
    padding: $spacing-base;

    &:last-child {
        margin-bottom: 0;
    }
}

.well-lg {
    padding: ($spacing-base + 5);
}

.well-sm {
    padding: ($spacing-base - 10);
}

.well-primary {
    background-color: $colorPrimary;
    color: #fff;
}