.trigger {
  height: 100%;
  display: flex;
  flex-direction: column;

  .panel-heading {
    margin: unset;
    padding: 20px 20px 15px;
    border-bottom: 0px;
  }

  .panel-body {
    flex: 1 1 auto;
  }

  .panel-footer {
    padding: 20px 20px 15px;
  }

  .media-object {
    max-width: unset;
  }
}

.trigger-wrapper {
  @include make-xs-column(12);
  @include make-sm-column(6);
  @include make-md-column(4);

  .section-side & {
    @include make-md-column(6);
  }
}

/*------------------------------------*\
    $PANEL-TRIGGER
\*------------------------------------*/
.panel-trigger-fixed,
.panel-trigger-fluid {
  overflow: hidden;

  .panel-heading {
    border-color: transparent;
  }

  .trigger-subtitle {
    color: $headings-color;
    display: block;
    font-style: normal;
    margin-bottom: $spacing-base;
  }

  .panel-heading + .panel-body,
  .panel-body + .panel-footer {
    padding-top: ($spacing-base / 2);
  }

  .panel-footer {
    border-color: transparent;
  }
}

.panel-trigger-fixed {
  min-height: 300px;
  margin-left: auto;
  margin-right: auto;
  max-width: 480px;
  position: relative;

  @media (max-width: $screen-sm-min) {
    margin-left: 0;
  }

  .panel-body {
    padding-bottom: 56.5px;
  }

  .panel-footer {
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
  }

  .media > .pull-right,
  .media > .pull-left {
    max-width: 25%;
  }
}

.panel-trigger-fluid {
  .trigger-bottom-right {
    padding-top: 10px;

    @media (min-width: $screen-sm-min) {
      padding-top: 0;
      text-align: right;
    }
  }
}

a.panel-trigger-fixed,
a.panel-trigger-fluid {
  color: $text-color;
  display: block;
  text-decoration: none;
}

/*------------------------------------*\
    $TRIGGER-DEFAULT
\*------------------------------------*/
.trigger-default {
  .panel-footer {
    background-color: #fff;
  }
}

/*------------------------------------*\
    $TRIGGER-VARIANTS
\*------------------------------------*/
.trigger-primary {
  @include trigger-variant($colorPrimary, #fff);
}

.trigger-secondary {
  @include trigger-variant($colorSecondary, #fff);
}

.trigger-alternate {
  @include trigger-variant($swatchBlueDark, #fff);
}

/*------------------------------------*\
    $TRIGGER-ILLUSTRATED
\*------------------------------------*/
.trigger-illustrated,
.trigger-illustrated-fluid {
  .image-cover {
    @include border-bottom-radius($panel-border-radius);
    background-position: center;
    background-size: cover;
    bottom: 0;
    position: absolute;
    left: 0;
    right: 0;
  }

  .panel-body {
    position: relative;
    z-index: 1;
  }
}

.trigger-illustrated {
  .panel-heading {
    height: 98px;
  }

  .image-cover {
    height: 200px;
  }

  .panel-body {
    color: #fff;
    height: 130px;
    overflow: hidden;

    ul, ol {
      padding-left: 16px;
    }
  }
}

.trigger-illustrated-fluid {
  .panel-heading {
    border-color: $panel-default-border;
  }

  .panel-body {
    background-color: #fff;
  }

  .image-cover {
    height: 160px;
  }
}

/*------------------------------------*\
    $TRIGGER-BANNER
\*------------------------------------*/
.trigger-banner {
  margin-left: auto;
  margin-right: auto;
  max-width: 480px;

  .panel-heading {
    border: none;
    height: 98px;
  }

  .image-cover {
    border-radius: 0 $panel-border-radius $panel-border-radius $panel-border-radius;
    background-position: center;
    background-size: cover;
    height: 298px;

    &.small-background {
      border-radius: 0 0 $panel-border-radius $panel-border-radius;
      height: 200px;
    }

    .play-button {
      position: relative;
    }
  }
}

.trigger-banner--half {
  .image-cover {
    height: 136.5px;
  }
}
