/* ==========================================================================
   Base - Typography
   ========================================================================== */
/*
  Naast de standaard Bootstrap-typography styling, gebruiken wij nog een paar andere elementen:
*/

h4, .h4,
h5, .h5,
h6, .h6 {
  color: $text-color;
  font-weight: $font-weight-regular;
  margin-bottom: ($line-height-computed / 2) - 8;
  margin-top: ($line-height-computed / 2) + 3;
}

.lead {
  font-family: "Proxima Nova Light", Verdana, Arial;
  font-size: $font-size-large;
  font-weight: 150;
  line-height: 1.6;
  text-shadow: 1px 1px 1px #fff
}

.text-muted {
  color: $colorGreyDark;
}
.text-grey-medium-dark {
  color: $swatchGreyMediumDark;
}
.text-primary {
  &,
  &:hover {
    color: $colorPrimary;
  }
}
.text-secondary {
  color: $colorSecondary;
}
.text-attention {
  color: $colorAttention;
}
.text-alternate {
  color: $colorAttentionAlt;
}
.text-white,
a.text-white:hover,
a.text-white:focus {
  color: $colorWhite;
}

a.text-white--border {
  border-bottom: 1px solid #fff;
  text-decoration: none;

  .icon {
    margin-right: 4px;
    position: relative;
    top: -2px;
  }

  &:hover,
  &:focus {
    border-color: transparent;
  }
}

.extra-line-height {
  line-height: 3;
}


/*
    Voor kader die extra aandacht nodig hebben:
*/
.bg-alternate,
.bg-attention,
.bg-dark,
.bg-secondary,
.bg-primary {
  color: $colorWhite;
  // Fix de aliasing van het font:
  @include font-smoothing;

  .h6, .h5, .h4, .h3, .h2, .h1,
  h6, h5, h4, h3, h2, h1 {
    color: $colorWhite;
  }

  .btn-link {
    color: $colorWhite;
  }

}
.bg-secondary {
  background-color: $colorSecondary !important;
}
.bg-secondary-dark {
  background-color: $swatchBlueDark;
}
.bg-attention {
  background-color: $colorAttention;
}
.bg-alternate {
  background-color: $colorAttentionAlt;
}
.bg-dark {
  background-color: $swatchGreyDark;
}
.bg-muted {
  background-color: $swatchGrey;
}
.bg-white {
  background-color: #fff;
}
.bg-alt {
  background-color: $colorBackgroundAlt !important;
}

.panel-secondary {
	.bg-secondary {
		color: $colorSecondary;
		background-color: $colorWhite;
	}
	a:hover.bg-secondary {
		color: $colorPrimary;
	}
	a:focus.bg-secondary {
		color: $colorPrimary;
	}
}

a.none,
.a.none {
  text-decoration: none;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.bg-alternate a:link,
.bg-alternate a:visited {
	color: $colorWhite;
	font-weight: normal;
}

.size-h1 { font-size: $font-size-h1; }
.size-h2 { font-size: $font-size-h2; }
.size-h3 { font-size: $font-size-h3; }
.size-h4 { font-size: $font-size-h4; }

.page-title {
  font-size: $font-size-h1;
  word-wrap: break-word;

  @media (min-width: $screen-sm-min) {
      font-size: $font-size-h1;
      word-wrap: normal;
  }
}
