html,
body {
    min-height: 100%;

    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
}
body {
    margin: 0;
    min-height: 100%;
    padding: 0;
    position: relative;
    overflow-x: hidden;
}

/* Behaviour
--------------------------------------------------------------*/
html {
    -webkit-tap-highlight-color: rgba($colorSecondary, .33);
    overflow-y: initial;

    &.is-ie-9 {
        overflow-y: auto;
    }
}


::-moz-selection {
    background-color: rgba($colorSecondary, .66);
    color: $colorWhite;
    text-shadow: none;
}
::selection {
    background-color: rgba($colorSecondary, .66);
    color: $colorWhite;
    text-shadow: none;
}
:focus {
    //outline: thin dotted; commented because of MSIE standard behaviour of outlining any element
}

/* Hyperlinks
--------------------------------------------------------------*/
a,
.anchor {
    text-decoration: underline;

    &.active,
    &:active,
    &:hover,
    &:focus {
        text-decoration: none;
    }

    &:focus {
        outline: 0;
    }

	/* Fix for removing underline at links that contain steps or columns */
	&:link[href^="#"] { text-decoration: none; }
}