.logo-intro-wrapper {
    background: $colorPrimary;
    border-radius: 10px;
    display: none;
    left: 17px;
    max-height: 16px;
    overflow: hidden;
    position: absolute;
    transition: all 1s $transition-timing;
    top: 46px;
    width: 84px;
    z-index: 1;

    &.open {
        max-height: 400px;
        width: 298px;

        .logo-intro {
            @include opacity(1);
        }
    }

    @media (min-width: $screen-sm-min) {
        display: block;
    }

    @media (min-width: $screen-md-min) {
        left: 19px;
        max-height: 22px;
        top: 57px;
        width: 119px;
    }

    @media (min-width: $screen-lg-min) {
        left: 20px;
        top: 59px;
    }
}

.logo-intro {
    @include font-smoothing;
    color: #fff;
    display: block;
    font-size: $font-size-base + 1;
    font-weight: $font-weight-base;
    line-height: 1.3;
    padding: 20px 18px;
    position: relative;
    text-transform: none;
    text-decoration: none;

    &:hover,
    &:focus {
        color: #fff;

        .hyperlink {
            text-decoration: underline;
        }
    }

    .title-wrapper {
        min-height: 14px;
    }

    .title {
        color: #fff;
        margin-bottom: 8px;
        margin-top: 0;
    }

    .hyperlink {
        color: $colorSecondary;
        display: block;
        font-weight: $font-weight-regular;
        margin-top: 16px;
        white-space: nowrap;
    }

    .close {
        color: #fff;
        padding: 8px;
        position: absolute;
        right: 0;
        text-shadow: none;
        top: 1px;
    }
}

.logo-intro--sm {
    @include opacity(0);
    min-width: 298px;
    transition: opacity 1s $transition-timing;

    .hyperlink {
        font-size: $font-size-base;
    }
}

.logo-intro--xs {
    background: $colorPrimary;
    border-radius: 0 10px 10px 10px;
    margin-left: 6px;
    max-width: 300px;
}

.logo-intro-collapse {
    margin-top: -16px;
}

/*------------------------------------*\
    $THEME-OVERWRITES
\*------------------------------------*/
.mobile-header .mobile-sticky .logo-intro {
    font-weight: normal;
    text-transform: none;

    &,
    span {
        font-size: $font-size-base;
    }
}