// Navs
// Nav Tabs

@mixin nav-tabs-variant($color, $active-color, $background-color, $link-border-color) {
  border-bottom: none;
  > li {
    > a {
      color: $color;

      &:hover,
      &:focus {
        text-decoration: none;
        background-color: transparent;
        border-color: transparent;
      }
    }

    &.active > a {
      color: $active-color;
      background-color: $background-color;
      border-color: $link-border-color;

      &:hover,
      &:focus {
        color: $active-color;
        text-decoration: none;
        background-color: $background-color;
        border-color: $link-border-color;
      }

    }
  }
}

@mixin tab-content-variant($border-radius, $background-color, $color) {
  border-radius: 0 $border-radius $border-radius $border-radius;
  background-color: $background-color;

  .tab-pane-body {
    color: $color;
  }
}
