div.mega-menu-container {
    position: relative;

    &.mega-open {
        background-color: #fff;
        border-top: 1px solid $swatchGreyLight;
        border-bottom: 1px solid $swatchGreyLight;
        nav.mega-menu {
            max-height: 400px;

            div.menu-columns {
                max-height: 400px;
            }
        }
    }

    nav.mega-menu {
        background-color: #fff;
        display: none;
        max-height: 0;
        overflow: hidden;
        transition: all 0.8s;
        width: 100%;
        z-index: 10;

        // Home link and icon
        div.home-link {
            @include fontBold();
            margin: 20px 0 15px 0;
        }

        div.menu-columns {
            transition: all 0.8s;
            -moz-column-count: 3;
            -webkit-column-count: 3;
            column-count: 3;
            width: 100%;
            max-height: 0;

            // Sitemap links
            ul {
                padding: 0;
                float: left;
                width: 240px;

                li {
                    list-style: none;
                    margin: 10px 0;
                    width: 100%;

                    &.head {
                        @include fontBold();
                        text-transform: uppercase;
                    }

                    @include fontLight();
                    font-size: $font-size-base;
                    a {
                        color: $swatchGreyDark;
                    }
                }
            }
        }
    }

    nav.mega-menu.active {
        display: block;
    }
}