.icon {
  display: inline-block;
  font-family: 'icons-liander-nl';
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  position: relative;
  text-align: center;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  &:before {
    display: inline-block;
    width: 1em;

    table & {
      width: auto;
    }
  }
}

