.panelSmsNotify {
    height: auto;
    padding-bottom: 0;
}


form.sms-notify {
    margin: 0;
    padding: 0;

    label {
        font-weight: 400;
    }

    div.phoneNumber,
    div.address {

        label {
            display: block;
        }

        span.prefix {
            background: rgba(0,0,0,.15);
            color: #fff;
            display: block;
            float: left;
            font-size: 16px;
            margin-right: 1px;
            padding: 16px;
        }

        input {
            background: rgba(0,0,0,.15);
            border: 0;
            color: rgba(255,255,255,.9);
            font-size: 16px;
            outline: 0;
            padding: 16px;

            @include placeholder(rgba(255,255,255,.3));
        }

        input:focus {
            color: rgba(255,255,255,1);
        }

        input::before {
            border: 1px solid yellow;
            content: '06';
            display: block;
        }

        div.postcode {
            float: left;
            margin-right: 22px;

            input {
                padding: 8px;
                width: 96px;
            }
        }

        div.number {
            float: left;

            input {
                margin-right: 4px;
                padding: 8px;
                width: 66px;
            }
        }
    }

    div.address {
        @include clearfix();
    }

    div.subscribe {
        @include clearfix();
        margin-bottom: 16px;

        input {
            float: left;
            margin-right: 6px;
        }
    }

    button.btn.btn-default {
        display: block;
        margin-bottom: 16px;
        min-width: 240px;
        position: static;
        right: 0;
        text-align: left;
        top: initial;

        span.icon-arrow-right {
            float: right;
            top: 6px;
        }
    }
}


@media ( min-width: $screen-sm ) {
    .panelSmsNotify {
        button.btn.btn-default {
            float: left;
        }

        .conditions {
            float: right;
            position: relative;
            top: 8px;
        }
    }
}