.dl-horizontal {
    dt,
    dd {
        line-height: ($line-height-base);
        text-align: left;
    }

    dt {
        @include text-overflow();
        color: $swatchPurple;
        float: left;
        font-weight: normal;
        text-align: left;
        width: ($component-offset-horizontal - 20);
    }

    dd {
        @include clearfix();
        margin-bottom: 10px;
        margin-left: 40%;
    }
}