.pagination-wrapper {
    @include clearfix();
    margin-bottom: 30px;
    margin-top: 0;
    position: relative;
    text-align: center;

    > .btn {
        border-radius: 0;
        float: left;
        font-weight: $font-weight-base;
        left: 0;
        min-width: 100px; // Beide knoppen zijn dan even breed
        padding-bottom: $padding-base-vertical - 2;
        padding-top: $padding-base-vertical - 2;
        position: absolute;

        & + .btn {
            left: auto;
            right: 0;
        }
    }

    .pagination {
        margin: 0;
    }

    .icon {
        display: none;
    }

    .ajax-progress-throbber {
        display: none;
    }
}

.pagination {
    border-radius: 0;

    > li {
        display: inline-block;
        padding: 0 3px 0 2px;

        > a,
        > span {
            display: block;
            line-height: $line-height-base;
            padding: $padding-base-vertical 0;
            min-width: 36px;
            text-align: center;

            &:first-child {
                border-radius: 0;
            }
            &:last-child {
                border-radius: 0;
            }

            &.spacer:hover,
            &.spacer:focus {
                background-color: transparent;
            }
        }

        /*a*/.progress-disabled {
            float: left;
        }
    }
}

@media (min-width: $screen-xs-min) {
    .pagination-wrapper {
        > .btn {
            min-width: 115px;
        }

        .pagination {
            margin: 0 130px; // Breedte van knoppen 'vorige' en 'volgende'
        }

        .icon {
            display: inline-block;
        }
    }
}

/*------------------------------------*\
    $OUTAGES
\*------------------------------------*/
.pagination-outages {
    ul li {
        display: none;

        &.active {
            display: inline-block;
        }
    }

    @media (min-width: $screen-sm-min) {
        ul li {
            display: inline-block;
        }
    }
}