.cloud--contact-wizard {
    color: #fff;

    .contact-wizard__business-select {
        display: block;
    }
}

.contact-wizard {
    .collapsable-icon {
        margin-left: 4px;
    }

    .collapsable-title,
    .collapsable-content {
        padding-right: 12px;
    }
}

.contact-wizard__business-select {
    @include animation(fadeIn ease-out .5s 2s both);
    position: absolute;
    right: 30px;
    top: -102px;
    width: 244px;
}

.banner .cloud--contact-wizard {
    float: right;
    padding-bottom: 12px;
    padding-top: 8px;
    position: relative;
    width: 700px;
}

.contact-wizard__multiple {
    position: relative;
    z-index: $zindex-dropdown + 1;
}

.contact-wizard__dropdown-sections {
    left: -1px;
    margin-top: -4px;
    min-width: 0;
    padding-top: 4px;
    right: -1px;

    > li > a {
        color: $colorPrimary;
        font-size: 15px;
        padding-left: 14px;
        padding-right: 14px;
        text-decoration: none;
    }
}

.contact-wizard__disabled {
    @include opacity(.3);
    pointer-events: none;
}

.contact-wizard__btn-row {
    @include make-row(6px);
}

.contact-wizard__btn-col {
    @include make-xs-column(6, 6px);
    @include make-sm-column(3, 6px);
}

.contact-wizard__cta-col {
    padding: 10px;
}

.contact-wizard__default-content {
    background: #fff;
    color: $text-color;
    padding: $spacing-base;
}

.contact-wizard__cta {
    font-size: 16px;
}

.contact-wizard__extra .icon {
    position: relative;
    top: 3px;
}

@media (max-width: $screen-sm-max) {
    .banner .cloud--contact-wizard {
        float: none;
        margin: 120px auto 0;
        right: auto;
        width: auto;
    }

    .contact-wizard__btn-col {
        padding-bottom: 3px;
        padding-top: 3px;
    }
}

@media (max-width: ($screen-xs-min - 1)) {
    .contact-wizard {
        .collapsable-content {
            padding-left: 12px;
        }
    }

    .contact-wizard__btn-col,
    .contact-wizard__cta-col {
        float: none;
        width: 100%;
    }

    .contact-wizard__cta-col {
        padding-bottom: 3px;
        padding-top: 3px;
    }

    .contact-wizard__cta {
        margin-left: auto;
        margin-right: auto;
        max-width: 250px;
    }
}
