/**
 * Breadcrumb bar
 */
.container-breadcrumbs {
    @include gradient-vertical(
        $swatchGreyLight,
        $swatchGreyMediumLight,
        0%,
        8%
    );
    min-height: 50px;

    .container.double-label {
        max-width: 970px;
    }
    .container-breadcrumbs-inner {
        max-width: 970px;
    }
    .breadcrumb {
        padding: 15px;
        padding-left: 0;
        margin-bottom: 0;
    }
    .breadcrumb > li + li:before {
        font-family: "FontAwesome";
        color: $swatchGreyMediumDark;
    }

    li {
        font-size: $font-size-base;
        span.glyphicon-home {
            font-size: 12px;
            width: 12px;
            height: 12px;
        }
        + li:before {
            padding: 0 10px;
        }
        span {
            margin-right: 10px;
        }
        a {
            color: $swatchBlue;
            text-decoration: none;
        }
    }
}
