nav#side-nav {
    .nav-side {
        padding-top: $spacing-base;
    }

    .nav-side__header {
        color: $swatchBlue;
        @include fontBold();
        font-weight: $font-weight-bold;
        font-size: $font-size-base;
        margin-bottom: 15px;
        a {
            text-transform: uppercase;
            text-decoration: none;
        }
    }

    .nav-side__list {
        border-top: 1px solid $swatchGreyLight;
        border-right: 1px solid $swatchGreyLight;
        padding-left: 0;
        list-style-type: none;

        li {
            display: block;

            &.active > .nav-side__anchor {
              color: $swatchBlue;
            }
        }
    }

    .nav-side__anchor {
        @include fontBold();
        font-weight: $font-weight-bold;
        font-size: $font-size-base;
        border-bottom: 1px solid $swatchGreyLight;
        color: $swatchGreyDark;
        display: block;
        line-height: 1.3;
        padding: 1.1em 2.2em;
        padding-left: 0;
        position: relative;
        text-decoration: none;
        transition: color $transition-duration $transition-timing;

        &.active {
            color: $swatchBlue;
        }
        &:hover {
            color: $swatchPurple;
        }

        .icon {
            font-size: 24px;
            margin-top: -14px;
            color: #a8a8a8;
            position: absolute;
            right: 15px;
            top: 50%;
        }
    }

    .nav-side__sub-list {
        padding-left: 0;
    }

    .nav-side__sub-anchor {
        @include fontLight();
        font-weight: $font-weight-lighter;
        font-size: $font-size-base;
        background-color: $swatchGreyMediumLight;
        border-bottom: 1px solid $swatchGreyLight;
        color: $swatchGreyDark;
        display: block;
        padding: 1em 20px;
        text-decoration: none;

        &.active {
            color: $swatchBlue;
        }
        &:hover {
            color: $swatchPurple;
        }
    }
}
