/*
    Custom Bootstrap labels

*/

.label {
    .h1 &, .h2 &, .h3 &, .h4 &, .h5 &, .h6 &,
    h1 &, h2 &, h3 &, h4 &, h5 &, h6 & {
        font-weight: inherit;
    }
}
.label--round {
    border-radius: 50%;
    display: inline-block;
    font-size: $font-size-medium;
    line-height: 30px;
    min-width: 30px;
    padding: 0;
    margin-right: 0;
}

.label--round--small {
    font-size: $font-size-extra-small;
    line-height: 21px;
    min-width: 21px;
}

@media (max-width: ($screen-xs-min - 1)) {
	.label--round--small {
		line-height: 29px;
		min-width: 29px;
	}
}
