/**
 * Part of mega-menu, styles the 'up' arrow below menu links
 */

.tri-up {
    /* Styling block element, not required */
    position: relative;
    border-top: 1px solid $swatchGreyLight;
    bottom: -24px;
    z-index: 1000;
    display: none;

    // center the arrow
    left: 50%;
    margin-left: -10px;
}

.double-label {
   .tri-up {
       bottom: -13px;
   }
}

/* Required for Down Triangle */
.tri-up:before, .tri-up:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent;
    border-top: 0;
}

/* Stroke */
.tri-up:before {
    top: -7px;
    left: 0;

    /* If 1px darken stroke slightly */
    border-bottom-color: $swatchGreyLight;
    border-width: 7px;
}

/* Fill */
.tri-up:after {
    top: -6px;
    left: 1px;
    border-bottom-color: #fff;
    border-width: 6px;
}
