/*------------------------------------*\
    $TWEET-BLOCK
\*------------------------------------*/
.tweet-block {
    padding: ($spacing-base * 2) 0;
}

.tweet-block__top {
    padding-bottom: $spacing-base;
}

.tweet-block__title {
    margin-bottom: $spacing-base - 11;
}

.tweet-block__footer {
    padding: 6px 10px 4px;
}

/*------------------------------------*\
    $TWEET
\*------------------------------------*/
.tweet {
    line-height: 1.5;
    padding: 21px 12px 21px 64px;
    position: relative;
}

.tweet__header {
    padding-bottom: 3px;
}

.tweet__user-img {
    left: 22px;
    max-width: 25px;
    position: absolute;
    top: 31px;
}

.tweet__user {
    color: $text-color;
    display: inline;
    font-size: $font-size-base;
    font-weight: $font-weight-medium;
    margin: 0;
}

.tweet__account {
    color: $colorPrimary;
    margin-left: 7px;
    text-decoration: none;
}

.tweet__time {
    margin-left: 7px;
}