.main-nav,
.main-nav-mobile {
  .main-nav-site-id {
    padding-left: 15px;

    img {
      height: 40px !important;
    }
  }
}

.main-nav {
  z-index: 9999;

  // XS and SM
  @media (max-width: $screen-sm-max) {
    background-color: #00749e;
    display: flex;
    flex-direction: column;
    height: 100vh;
    left: -80%;
    // padding: 0;
    overflow: auto;
    position: fixed;
    right: 100%;
    top: 0;
    transition: all .5s ease-out;

    &.open {
      left: 0;
      right: 20%;

      .main-nav-segments > li.active {
        left: 0;
        right: 0;
      }
    }

    .main-nav-level-1 {
      order: 1;
    }

    .container {
      margin: 0;
      padding: 0;
      width: 100%;
    }

    .main-nav-segments,
    .main-nav-utils,
    .main-nav-mobile-sections,
    .main-nav-mobile-sections ul {
      list-style-type: none;
      margin: 0;
      padding: 0;

      .main-nav-item-icon {
        bottom: 0;
        padding: 15px;
        pointer-events: none;
        position: absolute;
        right: 0;
        text-align: center;
        top: 0;
        width: 50px;
      }
    }

    .main-nav-sections {
      display: none;
    }

    .main-nav-segments {
      > li {
        background-color: $swatchBlue;
        position: relative;

        &.active {
          position: absolute;
          left: -80%;
          right: 100%;
          top: 0;
          transition: all .5s ease-out;
        }

        > a {
          border-bottom: 1px solid #00749e;
          color: $colorWhite;
          display: block;
          font-size: 1.13em;
          padding: 15px;
          text-decoration: none;
        }

        .main-nav-item-icon {
          color: white;
          font-size: 1.13em;
        }

        .main-nav-segment-pointer {
          display: none;
        }
      }
    }

    .main-nav-utils {
      > li {
        background-color: #00749e;
        position: relative;

        > a {
          border-bottom: 1px solid $swatchBlue;
          color: $colorWhite;
          display: block;
          font-size: 1.13em;
          padding: 15px;
          text-decoration: none;
        }

        .main-nav-item-icon {
          color: white;
          font-size: 1.13em;
        }

        .main-nav-segment-pointer {
          display: none;
        }
      }
    }

    .main-nav-mobile-sections {
      padding-top: 56px;

      > li {
        background-color: $colorWhite;
        position: relative;

        &.expanded,
        &.collapsed {
          > a {
            padding-right: 65px;
          }
        }

        &.expanded {
          > ul {
            max-height: 100vh;
          }
        }

        > a {
          border-bottom: 1px solid $swatchGrey;
          color: $swatchBlue;
          display: block;
          padding: 15px 50px 15px 30px;
          text-decoration: none;
        }

        .main-nav-item-icon,
        .sub-menu-toggle {
          color: $swatchBlue;
        }

        .sub-menu-toggle {
          border-left: 1px solid $swatchGrey;
          bottom: 0;
          padding: 15px;
          position: absolute;
          right: 0;
          text-align: center;
          top: 0;
          width: 50px;
        }

        .main-nav-segment-pointer {
          display: none;
        }
      }
    }

    // sub-menu
    .main-nav-mobile-sections ul {
      max-height: 0;
      overflow: hidden;
      position: relative;
      transition: max-height .5s ease-in-out;

      > li {
        border-bottom: 1px solid $swatchGrey;
        position: relative;
        background-color: $colorWhite;

        > a {
          color: $swatchGreyDark;
          display: block;
          padding: 15px 50px 15px 45px;
          text-decoration: none;
        }

        .main-nav-item-icon {
          color: $swatchGreyDark;
        }
      }
    }

    .main-nav-site-id {
      display: none;
    }
    .main-nav-search {
      display: none;
    }
  }
  // MD and LG
  @media (min-width: $screen-md-min) {
    background-color: $colorWhite;

    &.sticky {
      box-shadow: 0 4px 6px -4px #929292;
      position: fixed;
      top: 0;
      width: 100%;

      .main-nav-level-1 {
        display: none;
      }
    }

    .main-nav-level-1 {
      border-bottom: 1px solid $swatchGrey;

      .main-nav-container {
        display: flex;
        justify-content: space-between;
      }
    }

    .main-nav-sections,
    .main-nav-segments,
    .main-nav-utils {
      list-style-type: none;
      padding: 0;

      .main-nav-item-icon {
        display: none;
      }
    }

    .main-nav-mobile-sections {
      display: none;
    }

    .main-nav-segments,
    .main-nav-utils {
      display: flex;
      margin: 0;

      > li {
        cursor: pointer;

        > a {
          color: $swatchGreyDark;
          font-size: 14px;
          text-decoration: none;
        }
      }
    }
    .main-nav-segments {
      > li {
        padding: 5px 20px 7px;
        position: relative;

        &.active,
        &:hover {
          background-color: #00749e;

          > a {
            color: $colorWhite;
          }
        }

        &.active {
          .main-nav-segment-pointer {
            color: #00749e;
            display: block;
            left: 0;
            position: absolute;
            text-align: center;
            top: 26px;
            width: 100%;
          }
        }

        .main-nav-segment-pointer {
          display: none;
        }
      }
    }
    .main-nav-utils {
      > li {
        padding: 5px 15px 7px;

        &:hover {
          > a {
            color: $swatchBlue;
          }
        }
      }
    }

    .main-nav-level-2 {
      .main-nav-container {
        display: flex;
        justify-content: space-between;
        padding: 15px 0;
      }
    }

    .main-nav-sections {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin: 8px 0; // auto 0 doesn't work in IE11 with flex

      > li {
        padding: 0 15px;

        > a {
          color: $swatchGreyDark;
          font-family: "Proxima Nova SemiBold", Verdana, sans-serif;
          font-size: 17px;
          text-decoration: none;

          &.active,
          &:hover {
            color: $swatchBlue;
          }
        }

        .sub-menu-toggle {
          display: none;
        }
      }

      ul {
        display: none;
      }
    }
    .main-nav-search {
      margin: 3.5px 0; // auto 0 doesn't work in IE11 with flex

      /* Liander CSS fixes */
      .form-group {
        margin-bottom: 0;
      }
      .form-control-feedback {
        top: 0;
      }
    }
  }
}

.main-nav-mobile {
  background-color: $colorWhite;
  box-shadow: 0 4px 6px -4px $swatchGreyDark;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9997;

  @media (min-width: $screen-md-min) {
    display: none;
  }

  .main-nav-container {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
  }

  .main-nav-buttons {
    margin: auto 0;
  }
}

.main-nav-mobile-search {
  border-top: 1px solid #eee;
  max-height: 0;
  overflow: hidden;
  transition: max-height .8s ease-in-out;

  &.open {
    max-height: 65px;
  }

  .form-group {
    margin-top: 15px;
    margin-right: 0;
    margin-bottom: 0; /* Liander CSS fix */
    margin-left: 15px;
  }

  .form-control-feedback {
    top: 0px; /* Liander CSS fix */
    line-height: 34px; /* Fix for Mopinion loading Font Awesome styles for a second time */
  }
}

.main-nav-dummy {
  display: block;
  height: 60px; /* Correction for content, main-nav is fixed */

  @media (min-width: $screen-md-min) {
    display: none;
    height: 105px; /* Correction for content, when header becomes fixed */

    &.sticky {
      display: block;
    }
  }
}

.main-nav-overlay {
  @media (max-width: $screen-sm-max) {
    position: fixed;
    left: 0;
    right: 0;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 9998;
  }
}
