.livechat {
  position: fixed;
  z-index: 10000;
  display: none;
  width: calc(100vw - #{$livechat-margin * 2});
  max-width: $livechat-max-width;
  height: calc(100vh -  #{$livechat-margin * 2});
  max-height: $livechat-max-height;
  margin: $livechat-margin;
  background-color: $livechat-bg;
  border-radius: 0 $livechat-border-radius $livechat-border-radius;
  box-shadow: 0 0 5px 2px rgba(204, 204, 204, 1);

  &.in {
    display: flex;
    flex-direction: column;
    bottom: 0;
    right: 0;
  }

  iframe {
    height: 100%;
    width: 100%;
    border: 0;
    border-radius: 0 0 $livechat-border-radius $livechat-border-radius;
  }
}

.livechat-header {
  padding: $livechat-header-padding-y $livechat-header-padding-x;
}

.livechat-body {
  display: flex;
  flex-grow: 1;
  height: 100%;
}

.livechat-start-button {
  position: fixed;
  display: none;
  cursor: pointer;
  z-index: 10000;

  &.in {
    display: block;
    bottom: 0;
    right: 0;
  }
}
