.flex-carousel {
  position: relative;
  overflow: hidden;
}

.flex-carousel-container {
  margin: 0 15px;
  overflow: hidden;
}

.flex-carousel-inner {
  position: relative;
  left: -100%;
  display: flex;
  transform: translateX(100%);

  @media (min-width: 768px) {
    left: -50%;
    transform: translateX(50%);
  }
}

.flex-carousel-inner.is-reversing {
  transform: translateX(-100%);

  @media (min-width: 768px) {
    transform: translateX(-50%);
  }
}

.flex-carousel-inner.is-set {
  transition: transform .5s cubic-bezier(.23, 1, .32, 1);
  transform: none;
}

.flex-carousel-item {
  flex: 1 0 100%;
  order: 2;

  @media (min-width: 768px) {
    flex-basis: 50%;
  }
}

.flex-carousel-item.is-ref {
  order: 1;
}

.flex-carousel-control-prev,
.flex-carousel-control-next {
  position: absolute;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15px;
  text-decoration: none;
}
.flex-carousel-control-prev {
  left: 0;
}
.flex-carousel-control-next {
  right: 0;
}
