.left-aligned-lists {
    ul,
    ol {
        padding-left: 15px;
    }
}

.main-buttons {
    @extend .list-unstyled;
    @include center-block();
    max-width: 250px;
    text-align: center;

    > li {
        padding-bottom: 5px;

        .icon {
            margin-left: -16px;
            margin-top: -3px;
            position: relative;
        }
    }

    @media (min-width: $screen-sm-min) {
        max-width: none;
/*         padding-bottom: 12px; */

        > li {
            display: inline-block;
            padding: 0 7px;
        }
    }
}

.sub-buttons {
    @extend .nav-justified;
    @extend .list-unstyled;
    @include center-block();
    max-width: 250px;

    @media (min-width: $screen-sm-min) {
        max-width: none;

        > li {
            padding-left: 4px;
            padding-right: 4px;

            a {
                max-width: 530px;
                margin: 0 auto;
            }
        }
    }
}

.list-lined {
    border-bottom: 1px solid $border-color;

    > li {
        border-top: 1px solid $border-color;
    }
}

.panel > .list-lined:first-child > li:first-child {
    border-top-width: 0;
}

.extra-item-height li{
	margin-bottom: 10px;
}

