.liander-in-kaart {
    margin-top: 24px;

    .legend {
        line-height: 23px;
    }

    .legend-symbol {
        border-radius: 3px;
        display: inline-block;
        height: 23px;
        margin-right: 6px;
        position: relative;
        top: -1px;
        width: 23px;
    }

    .legend-color-first {
        background-color: #008cbe;
    }

    .legend-color-second {
        background-color: #821e7d;
    }

    .legend-color-third {
        background-color: #e56c00;
    }

    .clickable-area {
        cursor: pointer;
        fill: transparent;
    }

    .map > svg {
        min-height: 320px;
        max-height: 100%;
        max-width: 100%;

        @media (min-width: $screen-xs-min) {
            min-height: 480px;
        }

        @media (min-width: $screen-sm-min) {
            min-height: 634px;
        }
    }
}

@media (min-width: $screen-sm-min) {
    .liander-in-kaart {
        position: relative;

        .legend {
            top: 0;
            left: 0;
            position: absolute
        }

        .quick-links {
            left: 0;
            bottom: 0;
            position: absolute;
        }
    }

}