.loader-centered {
    left: 0;
    margin-top: -24px;
    position: absolute;
    right: 0;
    text-align: center;
    top: 50%;
}

.loader-wrapper,
.loader {
    display: inline-block;
    text-align: center;
}

.loader {
    @include scale(0.6);
    height: 48px;
    position: relative;
    width: 48px;
}

.loader__item {
    @include vendor-webkit(animation, loader 1.04s 0s linear infinite);
    background-color: $colorPrimary;
    border-radius: 4px;
    height: 9px;
    position: absolute;
    width: 9px;
}

.loader__item--1 {
    @include vendor-webkit(animation-delay, 0.39s);
    left: 0;
    top: 20px;
}

.loader__item--2 {
    @include vendor-webkit(animation-delay, 0.52s);
    left: 6px;
    top: 6px;
}

.loader__item--3 {
    @include vendor-webkit(animation-delay, 0.65s);
    left: 20px;
    top: 0;
}

.loader__item--4 {
    @include vendor-webkit(animation-delay, 0.78s);
    right: 6px;
    top: 6px;
}

.loader__item--5 {
    @include vendor-webkit(animation-delay, 0.91s);
    right: 0;
    top: 20px;
}

.loader__item--6 {
    @include vendor-webkit(animation-delay, 1.04s);
    bottom: 6px;
    right: 6px;
}

.loader__item--7 {
    @include vendor-webkit(animation-delay, 1.17s);
    bottom: 0;
    left: 20px;
}

.loader__item--8 {
    @include vendor-webkit(animation-delay, 1.30s);
    bottom: 6px;
    left: 6px;
}