.document-preview {
    display: block;
    padding-bottom: 15px;
    padding-top: 14px;
    text-decoration: none;

    &,
    &:hover,
    &:focus {
        color: $text-color;
    }

    &:hover .btn,
    &:focus .btn {
        text-decoration: underline;
    }
}

.document-preview__date {
    @include opacity(.5);
    display: block;
}

.document-preview__date,
.document-preview__title {
    margin-top: 14px;
}

.document-preview__img {
    @extend .img-branded;
    margin-top: 3px;
    max-width: 50px;
}

ul.news-overview {
  li {
    border-bottom: 1px solid $border-color;
    transition: background-color $transition-duration $transition-timing;

    &:first-of-type {
      border-top: 1px solid $border-color;
    }

    &:hover {
        background-color: $colorGreyLight;
    }
  }

  .document-preview__img {
    margin-top: 0;
    min-width: 80px;
    max-width: 80px;
  }

  @media (max-width: $screen-xs-max) {
    .document-preview__img {
      min-width: 50px;
      max-width: 50px;
    }

    .pull-left {
      margin-bottom: 20px;
      min-height: 50px;
    }

    .media-body {
      overflow: visible;

      h3.media-heading {
        margin-bottom: 22px;
        min-height: 50px;
      }
    }
  }
}

@media (min-width: $screen-sm-min) {
    .document-preview__title {
        min-height: 44px;
    }

    .document-preview .media {
        min-height: 88px;
    }
}

@media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
    .document-preview__title {
        min-height: 66px;
    }

    .document-preview .media {
        min-height: 110px;
    }
}