.equal-columns {
    position: relative;
}

.equal-columns__col  {
    position: static;

    &:before {
        @include opacity(.2);
        background: #fff;
        bottom: 0;
        content: '';
        margin-left: -20px;
        position: absolute;
        top: 0;
        width: 1px;
    }

    &:first-child:before {
        display: none;
    }
}

.equal-columns__content  {
    padding-top: 15px;

    p {
        line-height: $line-height-computed - 6;
    }
}

@media (max-width: $screen-xs-max) {
    .equal-columns__col  {
        border-bottom: 1px solid rgba(#fff, .2);
        padding: 26px 0 32px;

        &:before {
            display: none;
        }

        &:first-child {
            padding-top: 6px;
        }

        &:last-child {
            border-bottom-width: 0;
            padding-bottom: 8px;
        }
    }

    .equal-columns__content  {
        padding: 2px 0 4px;
    }

    .well-lg .equal-columns {
        margin-left: -5px;
        margin-right: -5px;
    }
}

@media (min-width: $screen-sm-min) {
    .equal-columns {
        margin-bottom: 6px;
    }

    .equal-columns__col  {
        padding-bottom: 58px;

        .btn {
            bottom: 0;
            position: absolute;
        }
    }
}