.ps--relative   { position: relative; }
.clear--both { clear: both; }

// border
@mixin border ($color: $base-border-color, $width: 1px){
    border:$width solid $color;
}

// form submit button
@mixin form-submit-button{
    float: right;
    background: url(../images/sprite-01.png) no-repeat;
    width: 16px;
    height: 16px;
    padding: 0;
    outline: none;
    border:none;
    @include hide-text;
}

// responsive images
@mixin resp-image{
    display: block;
    width: 100%;
    height: auto;
}

// border radius
@mixin border-radius($radius: 10px) {
	border-radius: $radius;
}