.the-twist-underlay {
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: $zindex-navbar-fixed;
}

.the-twist {
    @include animation(shakeHorizontal 1s 0s ease-in-out infinite);
    @include animation-play-state(paused);

    display: none;
    max-width: 98px;
    padding-left: 72px;
    position: fixed;
    right: -108px;
    top: 200px;
    transition: max-width 1s $transition-timing, right 1s $transition-timing;
    z-index: $zindex-navbar-fixed;
}

.the-twist.sneak-in {
    right: -8px;
    cursor: pointer;
}

html:not(.is-device) .the-twist.sneak-in:hover:not(.slide-in) {
    @include animation-play-state(running);
}

.the-twist-image {
    width: 540px;

    img {
        border-radius: $border-radius-large;
    }
}

.the-twist-tip {
    @extend .cloud;
    @extend .cloud--attention;
    @extend .bottom-right;

    @include opacity(1);

    color: #fff;
    font-size: $font-size-h3;
    padding: 12px 16px 12px 10px;
    left: 0;
    position: absolute;
    text-transform: uppercase;
    top: 52px;
    transition: left ease-out 1s, opacity ease-in 1s;
    white-space: nowrap;
    z-index: 1;

    .icon {
        vertical-align: middle;
    }
}

.the-twist-body {
    bottom: 100%;
    left: 0;
    overflow: hidden;
    position: absolute;
    transition: bottom 1s $transition-timing;
    top: 52px;
    width: 270px;
    z-index: 1;
}

.the-twist-content {
    @extend .cloud;
    @extend .cloud--attention;
    @extend .bottom-right;

    padding-bottom: 78px; /* 30 + 40 + 8 (container padding + button height + p margin ) */
}

.the-twist-btn {
    position: absolute;
    bottom: 30px;
}

.the-twist-close {
    @extend .btn;
    @extend .btn-contrast-blue;
    @include opacity(0);

    position: absolute;
    right: -100px;
    top: 12px;
    transition: all $transition-duration ease, opacity 1s $transition-timing, right 1s $transition-timing;
    z-index: 1;
}

.the-twist.slide-in {
    cursor: auto;
    max-width: 610px;
}

.the-twist.show-content {
    .the-twist-body {
        bottom: 0;
    }

    .the-twist-tip {
      @include opacity(0);
      left: 0 !important;
    }

    .the-twist-close {
        @include opacity(1);
        right: 12px;
    }
}

@media (min-width: $screen-sm-min) {
    .the-twist {
        display: block;
    }
}
