.entry-point {
    @include clearfix();
    display: block;
    padding: ($spacing-base + 4) $spacing-base $spacing-base;
}

a.entry-point {
    text-decoration: none;
    transition: background-color $transition-duration $transition-timing;

    &:hover,
    &:focus {
        background-color: $colorGreyLight;
    }
}

.entry-point__img {
    float: right;
    margin-left: 16px;
    max-width: 50px;
}

.entry-point__title {
    margin-bottom: 6px;
    margin-top: 0;
}

.entry-point__desc {
    @include opacity(.5);
}

@media (min-width: $screen-md-min) {
    .entry-point {
        min-height: 130px;
    }

    .entry-point__img {
        max-width: 40px;
    }
}