// Tabs
// -------------------------

// Tab Content

.tab-pane-body {
  padding: $tab-pane-body-padding;
}

// Tab Contextuals

.nav-tabs-primary {
  @include nav-tabs-variant($nav-tabs-primary-color, $nav-tabs-primary-active-color, $nav-tabs-primary-background-color, $nav-tabs-primary-link-border-color);
}

.nav-tabs-secondary {
  @include nav-tabs-variant($nav-tabs-secondary-color, $nav-tabs-secondary-active-color, $nav-tabs-secondary-background-color, $nav-tabs-secondary-link-border-color);
}

.tab-content-primary {
  @include tab-content-variant($tab-content-border-radius, $tab-content-primary-background-color, $tab-content-primary-color);
}

.tab-content-secondary {
  @include tab-content-variant($tab-content-border-radius, $tab-content-secondary-background-color, $tab-content-secondary-color);
}
