.manuals-preview,
.manuals-preview img,
.manuals-preview__img-wrapper {
    transition: all .3s $transition-timing;
}

.manuals-preview {
    max-width: 60px;
    position: relative;

    img {
        max-height: 60px;
    }

    .manuals-preview__zoom-out {
        display: none;
    }
}

.manuals-preview--large {
    max-width: 100%;

    img {
        max-height: 100%;
    }

    .manuals-preview__img-wrapper {
        max-width: 100%;
    }

    .manuals-preview__zoom-in {
        display: none;
    }

    .manuals-preview__zoom-out {
        display: inline-block;
    }
}

.manuals-preview__img-wrapper {
    display: inline-block;
    max-width: 60px;
    position: relative;

    .icon {
        bottom: -8px;
        position: absolute;
        right: -8px;
    }
}

@media (min-width: $screen-sm-min) {
    .manuals-preview {
        text-align: center;

        img {
            max-height: none;
        }
    }
}