.numbered-list {
    counter-reset: li-counter;
    list-style-type: none;
    padding-left: 0;

    > li {
        padding-bottom: 2px;
        padding-left: 44px;
        padding-top: 2px;
        position: relative;

        &:last-child {
            padding-bottom: 0;
        }

        &:before {
            background: $colorSecondary;
            border-radius: 100%;
            color: #fff;
            content: counter(li-counter);
            counter-increment: li-counter;
            font-size: $font-size-base;
            left: 0;
            line-height: 30px;
            margin-top: -2px;
            position: absolute;
            text-align: center;
            top: 2px;
            width: 30px;
        }
    }

    > p {
      display: none;
    }
}

.numbered-list--large {
    > li {
        padding-bottom: 24px;
        padding-top: 24px;

        &:last-child {
            padding-bottom: 12px;
        }

        &:before {
            top: 24px;
        }
    }
}

.numbered-list__title {
    color: $swatchGreyDark;
    font-family: "Proxima Nova Semibold";
    margin-top: 0;
}

.numbered-list__unindent {
    margin-left: -44px;
}

.numbered-list--white {
    > li:before {
        background-color: #fff;
        color: $colorPrimary;
    }
}

.numbered-list--connected {
    > li:before {
      z-index: 1;
      }

    > li:after {
      position: absolute;
      content: '';
      top: 0;
      left: 0.94em;
      height: 100%;
      width: 1px;
      background-image: repeating-linear-gradient(180deg, transparent, transparent 3px, $swatchGrey 3px, $swatchGrey 6px);
      z-index: 0;
      }

    > li:last-child::after {
      display: none;
      }
}

.numbered-list--large.numbered-list--connected {
    > li:after {
      top: 24px;
      height: calc(100% - 24px);
      }

    > li:last-child::after {
      display: inline-block;
      height: 24px;
      top: 0;
      }
}
