@import "fonts";
@import "helpers";

// Load fontawesome
$fa-font-path: "../fonts";
@import "../../node_modules/font-awesome/scss/font-awesome";

@import "components/header";
@import "components/footer";
@import "components/nav-side";

/**
 * Base theme demo styles to be overridden
 */
body {
    background-color: #fff;
}
.main-container {
    background-color: #fff;
}

/* Fix scrollbar jumping */
html {
    overflow-y: scroll;
}

/**
 * Never outline anchors
 */
a, a:focus {
    outline: 0 none;
}
