.info-message {
    display: none;
    font-size: $font-size-base;
    position: absolute;
    line-height: 1.4;
    max-width: $tooltip-max-width;
    z-index: 8;

    &.in { display: block; }

    &.info-message-block { max-width: none; }

    $positions: top top-left top-right right bottom bottom-left bottom-right left;
    @each $position in $positions {
        &[data-position="#{$position}"] {
            @include info-message-position($position)
        }
    }

    @media (max-width: $screen-xs-max) {
        @each $position in $positions {
            &[data-position-xs="#{$position}"] {
                @include info-message-position($position)
            }
        }
    }

    @media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
        @each $position in $positions {
            &[data-position-sm="#{$position}"] {
                @include info-message-position($position)
            }
        }
    }

    @media (min-width: $screen-md-min) and (max-width: $screen-md-max) {
        @each $position in $positions {
            &[data-position-md="#{$position}"] {
                @include info-message-position($position)
            }
        }
    }

    @media (min-width: $screen-lg-min) {
        @each $position in $positions {
            &[data-position-lg="#{$position}"] {
                @include info-message-position($position)
            }
        }
    }
}

.info-message--relative {
    position: relative;
    max-width: none;
}

// Wrapper for the tooltip content
.info-message__inner {
    padding: 11px 16px;
    color: #fff;
    text-align: center;
    text-decoration: none;
    background-color: $colorAttention;
    border-radius: $border-radius-base;
}

// Arrows
.info-message__arrow {
    position: absolute;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
}