@media (max-width: $screen-xs-max) {

    .mobile-header {
        position: relative !important;
        top: 0 !important;
        //z-index: $zindex-navbar-fixed;

        .webform__submit {
            display: none;
        }

        .mobile-search {
            .form-control {
                height: 40px;
            }
            .navbar-form {
                margin-top: 5px;

                .form-group div:first-child {
                    padding-right: 0;
                }
                .form-group div:last-child {
                    padding-left: 0;
                }

                // We need to show it anyway. Temp fix
                &.hide-on-sticky {
                    display: block !important;
                }

                .form-group div.dropdown {
                    padding-left: 0;

                    .dropdown-menu {
                        box-shadow: none;
                        float: none;
                        margin: 0;
                        position: relative;
                    }
                }

                button {
                    background-color: $colorSecondary;
                    border-color: $colorSecondary;
                }
            }
        }

        .form-control-feedback {
            font-size: $font-size-medium;
            height: 40px;
            line-height: 40px;
            right: 60px;
            top: 0;
        }

        .dropdown-menu > li > a {
            padding-bottom: 6px;
            padding-top: 6px;
        }

        .mobile-top-item {
            margin-top: 3px;
            margin-left: 5px;
        }

        .row.nav.sub-level span.fa,
        .fa {
            color: #a8a8a8;
            font-size: 15px;
        }
        .fa {
            margin-top: -8px;
            position: absolute;
            top: 50%;
        }
        .fa-chevron-left,
        .back .fa {
            left: 15px;
            margin-top: -8px;
        }
        .fa-chevron-right,
        .fa-angle-right {
            right: 15px;
        }
        .row.nav ul li {
            line-height: 1.4;
            padding: 0;
            position: relative;

            a {
                padding: 18px 25px 18px 15px;

                i.fa-angle-right {
                    margin-top: -12px;
                }
            }

            &.back {
                padding: 0;

                a {
                    padding-left: 40px;
                    padding-right: 15px;
                }

                .fa-chevron-left {
                    padding: 0;
                }
            }

            &.header {
                margin-left: 15px;
                padding: 0;

                a {
                    padding-left: 0;
                }
            }

            &.item {
                margin-left: 30px;
                padding-left: 0;

                a {
                    padding-left: 0;
                }
            }

            span.label {
                color: white;
                position: absolute;
            }
        }
        &.mobile-menu-open {
            .main-level,
            .sub-level {
                max-height: 1000px;
            }
        }

        .row.nav.sub-level {
            ul:nth-child(2),
            ul:nth-child(3),
            ul:nth-child(4) {
                padding-left: 0;
            }

            ul:nth-child(2) {
                li.header {
                   padding: 18px 15px 18px 0;

                   a {
                    margin-bottom: -18px;
                    margin-top: -18px;
                   }
                }
            }
        }
    }

    .search-icon .btn {
        line-height: 1px;
        padding: 0;
        span {
            padding: 6px;
            font-size: 23px;
        }
    }

    .mobile-back {
        margin-left: 0;
        margin-right: 0;
        padding: 0;

        a {
            display: block;
            padding: 18px 15px;
        }

        span.fa-angle-left {
            margin-right: 10px;
            padding: 0;
            position: relative;
            top: 1px;
        }
    }

    .node-type-liander-home .mobile-back {
        display: none;
    }
}

