/* Skiplinks
--------------------------------------------------------------*/
.skiplinks {
    height: 0;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}
.skiplink {
    display: block;
    margin: 0;
    padding: 1em;
    position: absolute;
    top: -5em;
    width: auto;
    z-index: 10000;
}
.skiplink:focus {
    background-color: $colorPrimary;
    color: $colorWhite;
    top: 0;
}

/* Site header on desktop & mobile
--------------------------------------------------------------*/
.container-header,
.mobile-header {

    .dropdown-menu {
        width: 270px;
    }

    .dropdown .dropdown-menu {
        > li {
            > a {
                color: #525151;
                font-size: 11px;

                .apachesolr_autocomplete.message {
                    font-size: 10px;
                }
            }
        }
    }

    .form-control-feedback {
        @include opacity(.5);
    }
}

/* Site logo
--------------------------------------------------------------*/
.site-logo {
    @include make-xs-column(6);
    @include make-sm-column(12);
    margin-bottom: 0;
    margin-top: 0;
    position: relative;
    z-index: 1;
}

.site-logo__anchor {
    @include text-hide();
    display: block;
    float: left;
    max-width: 90px;
    position: relative;
    z-index: 2;

    @media (min-width: $screen-sm-min) {
        max-width: 110px;
    }

    @media (min-width: $screen-md-min) {
        max-width: 146px;
    }
}

/* Global footer
--------------------------------------------------------------*/
.global-footer {
    border-top: none;
    padding: $spacing-base 0;
}

/* Global header

    Deze <section> elementen overspannen de volledige browser-viewport.
    Ze bevatten altijd een `.container` element om een grid-row erbinnen te centereren/groeperen.
    Achtergrond-kleuren en padding zijn te varieren d.m.v. enkele classes:
--------------------------------------------------------------*/

.page-section {
    @include clearfix;

    padding-bottom: $spacing-base;
    padding-top: $spacing-base;
}
.section-first { padding-top: 0; }
.section-primary { background-color: transparent; }
.section-secondary { background-color: $colorGreyLight; }

/* 2 Kolommen
    ___ ___ ___ ___ ___ ___ ___ ___ ___ ___ ___ ___
   | Side (3)  | Body (9)                          |
   |           |                                   |
   |___ ___ ___|___ ___ ___ ___ ___ ___ ___ ___ ___|

    .page-side komt in de source EERST, vervolgens .page-body
--------------------------------------------------------------*/
.section-side {
    padding-top: 0;
    padding-bottom: 0;
}

.page-side {
    @media (min-width: $screen-md-min) {
      padding-right: 0;
    }
}

.page-main {
    @media (min-width: $screen-md-min) {
        padding-left: ($grid-gutter-width * 1.5);
        padding-top: 2px;
    }

    // If no menu sidebar is present, the page-main container is
    // centered.
    &.page-main-center {
        margin: 0 auto;
        float: none;

        // Left and right padding must be the same to be correctly
        // centered. A non-centered page-main has 30px left padding
        // and 10px right padding.
        padding-left: 20px;
        padding-right: 20px;
    }
}

/* Hide on homepage
--------------------------------------------------------------*/
.front .hide-on-front {
    display: none;
}

/* Alternate body background-color
--------------------------------------------------------------*/
.body-bg-alt {
    background-color: $colorBackgroundAlt;
    padding-top: 12px;
}

.body-bg-alt-bottom {
    background-color: $colorBackgroundAlt;
    position: relative;

    @media (min-width: $screen-md-min) {
        background-color: transparent;

        &:before {
            background-color: $colorBackgroundAlt;
            content: '';
            bottom: 0;
            height: 180px;
            left: 0;
            position: absolute;
            right: 0;
        }
    }
}
