.back-to-top {
  align-items: center;
  background-color: rgba(82, 81, 81, 0.5);
  border-radius: $border-radius-large;
  bottom: $spacing-base;
  cursor: pointer;
  display: flex;
  height: 45px;
  justify-content: center;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  right: $spacing-base;
  transition: opacity 1s ease-in;
  width: 45px;
}

.back-to-top-show {
  opacity: 1;
  pointer-events: auto;
}
