.right-splitted-column {
    padding-top: 12px;
}

.right-splitted-column--h1-btn {
    margin-top: -20px;
    padding-bottom: 20px;
    padding-top: 0;
}

@media (min-width: $screen-sm-min) {
    .right-splitted-column {
        padding-top: 0;
        text-align: right;
    }

    .right-splitted-column--h1-btn {
        margin-top: 0;
        padding-bottom: 0;
        padding-top: 20px;
    }
}

@media (min-width: $screen-md-min) {
    .right-splitted-column--h1-btn {
        padding-top: 26px;
    }
}