/**
    Part: Box

    Is:
      Een groeperend container-element.

    Context:
      Wordt gebruikt als 'kolom'-element. Meestal naast ander 'boxes'.

    Contains:
      - (optioneel) 1 headline (h1?)
      - (optioneel) 1 sub-headline (h2/3?)
      - 1 call-to-action (btn-secondary meestal)
      - ...

    States:
*/

.panel {

  .label-heading {
    margin-top: 10px;
    max-width: 70%;
    margin: 0 auto;
    line-height: 1.4;
  }

  .call-to-action {
    margin-top: 2em;
  }

  .panel {
    @include box-shadow(none);

    .panel-body {
      // remove padding in case of nested panels
      padding: 0;
    }
  }
  // Met Melving naar kijken waarom de eerst stap niet altijd "stap-0" is...
  .panel#stap-1,
  .panel#stap-0 {
    border: 0;
  }

  .panel-footer {
    border-top-width: 0;
  }

  &.flex-panel {
    height: 100%;
    padding-bottom: 54px; /* Evil hack. Reserve some space for a panel-footer with a normal button (most common use). Use JS for this? */

    .panel-footer {
      position: absolute;
      bottom: 0;
      width: 100%;
    }
  }
}

.panel-default,
.panel {
  @include box-shadow(0 0 2px 0 rgba($colorGrey, .75));
  background-color: $colorWhite;
  border-top-left-radius: 0;
  position: relative;
}

a.panel {
  display: block;
  text-decoration: none;
}

/*------------------------------------*\
    $ELEMENTEN
\*------------------------------------*/
.panel-title {
  color: $colorPrimary;
}

h1.panel-title, .h1.panel-title {
  font-size: $font-size-h1
}

h2.panel-title, .h2.panel-title {
  font-size: $font-size-h2
}

h3.panel-title, .h3.panel-title {
  font-size: $font-size-h3
}

h4.panel-title, .h4.panel-title {
  font-size: $font-size-h4
}

h5.panel-title, .h5.panel-title {
  font-size: $font-size-h5
}

h6.panel-title, .h6.panel-title {
  font-size: $font-size-h6
}

.panel-heading {
  margin: 0 $spacing-base;
  padding: $spacing-base 0 ($spacing-base - 5);

  .btn {
    position: relative;
    top: -6px;
  }

}

.panel-heading--large {
  margin-left: 30px;
  margin-right: 30px;
  padding-bottom: 4px;
  padding-top: 36px;
}

.panel-body--large {
  padding-bottom: $panel-body-padding + 24;
  padding-left: $panel-body-padding + 10;
  padding-right: $panel-body-padding + 10;
}

.ab-steps .panel-heading {
  border-bottom: 1px solid $colorGrey;
  .label-heading {
    margin: 0;
    max-width: 100%;
  }
}

.panel-body--step {
  padding-top: $spacing-base * 1.5;
  padding-bottom: $spacing-base * 2;
}

.panel-body--illustrated {
  padding-bottom: 80px;

  @media (min-width: $screen-sm-min) {
    padding-bottom: $panel-body-padding;
  }
}

.panel__title {
  padding-left: $panel-body-padding;
  padding-right: $panel-body-padding;

  .panel-body > & {
    padding-left: 0;
    padding-right: 0;
    margin-top: 5px;
  }
}

.panel-heading--borderless {
  border-bottom-width: 0;

}

.panel__sub-heading {
  display: block;
  font-style: normal;
}

.panel__illustration {
  float: right;
  margin-left: 12px;
  max-height: 210px;
  max-width: 162px;

  img {
    max-height: 210px;
    max-width: 150px;
  }
}

.panel__illustration-cover {
  bottom: -10px;
  left: 0;
  position: absolute;
  right: -200px;
  top: 0;

  img {
    bottom: 0;
    max-height: 90%;
    max-width: 90%;
    position: absolute;
    right: 0;
  }

  @media (min-width: $screen-xs-min) {
    bottom: 0;
    right: 0;
  }
}

.panel__illustration-bottom {
  bottom: 0;
  height: 200px;
  left: 0;
  position: absolute;
  right: 0;
  border-radius: 0px 0px 4px 4px;

  img {
    display: block;
    margin: 0 auto;
    max-height: 100%;
    max-width: none;
  }
}

.trigger15,
.trigger-header-white {
  .panel.panel--illustrated {
    border: 1px solid $panel-default-border;
    border-width: 1px;
  }
  .panel-body {
    padding-bottom: 0;
    padding-top: 0;
  }
  .panel__illustration-bottom {
    bottom: auto;

    img {
      max-width: 100%;
    }
  }
}

.trigger-location {
  .panel__illustration-bottom {
    img {
      @media (max-width: ($screen-xs-min - 1)) {
        /*max-height: none;*/
        min-width: 100%;
      }
      @media (min-width: $screen-lg-min) {
        min-width: 100%;
      }
    }
  }
}

.panel__body-img {
  float: right;
  margin-left: $spacing-base;
  max-height: 106px;
  max-width: 100px;

  img {
    @extend .img-responsive;
    max-height: 96px;
  }
}

.panel__body-img--wide {
  max-width: none;
  width: 30%;

  img {
    margin-left: auto;
    margin-right: auto;
  }
}

.panel__bottom-right {
  padding-top: 18px;

  @media (min-width: $screen-sm-min) {
    text-align: right;
  }
}

/*------------------------------------*\
    $MODIFIERS
\*------------------------------------*/
.panel-vertical {
  margin-bottom: $padding-small-vertical;
}

.panel--wide {
  .panel__illustration {
    position: absolute;
    max-height: 210px;
    max-width: 130px;
    right: -34px;

    img {
      max-height: 210px;
    }
  }

  .panel-body {
    position: relative;
    z-index: 1;
    padding-top: 8px;
  }

  .panel__description {
    padding-bottom: 0;
    padding-right: 100px;
  }

  @media (min-width: $screen-sm-min) {
    .panel__illustration {
      max-height: 290px;
      max-width: 220px;
      right: 0;

      img {
        max-height: 290px;
      }
    }

    .panel__description {
      padding-right: 240px;
    }
  }
}

.panel--illustrated {
  min-height: 302px;
  overflow: hidden;
  padding-bottom: 70px;

  &.panel--half {
    min-height: 150.5px;
  }
  &.panel-imageonly {
    padding-bottom: 0px;
    .panel__illustration-cover {
      img {
        max-height: 100%;
        max-width: 100%;
        width: 100%
      }
    }
  }

  &.panel-primary,
  &.panel-secondary,
  &.panel-alternate {
    &,
    h1, .h1,
    h2, .h2,
    h3, .h3 {
      color: #fff;
    }
  }

  h1, .h1,
  h2, .h2,
  h3, .h3,
  .heading-alignment {
    margin-top: 20px;
  }

  .btn {
    bottom: 15px;
    position: absolute;
    right: 15px;
    z-index: 1;
  }

  .panel-body {
    position: relative;
    z-index: 1;
    border-radius: 0px 4px 0px 0px;

    &.panel-body-white {
      background-color: $colorWhite;
      height: 95px;
      padding: 0px 20px;

      h2 {
        color: $colorPrimary;
      }
    }

  }

  &.panel--btn-blue .btn {
    @extend .btn-secondary;
  }

  &.panel--btn-white .btn {
    @extend .btn-contrast-blue;
  }

  &.panel--btn-transparent .btn {
    @extend .btn-link;
    @extend .link-white;
  }

  @media (min-width: $screen-sm-min) {
    min-height: 0;
    height: 302px;

    &.panel--half {
      min-height: 0;
      height: 139.5px;
    }
  }
}

.panel--supplier {
  display: block;
  min-height: 270px;
  text-decoration: none;
  transition: background-color $transition-duration $transition-timing;

  &,
  &:hover,
  &:focus {
    color: $swatchGreyDark;
  }

  &:hover,
  &:focus {
    background-color: $colorGreyLight;
  }

  &.no-anchor:hover,
  &.no-anchor:focus {
    background-color: transparent;
    cursor: default;
  }

  h3 {
    color: $swatchGreyDark;
    font-weight: $font-weight-medium;
    margin: 0;

    .icon {
      font-size: 24px;
      margin-right: 14px;
    }
  }

  .supplier-image {
    display: table-cell;
    padding: 24px 0;
    text-align: center;
    vertical-align: middle;

    img {
      max-height: 110px;
      max-width: 240px;
    }
  }

  @media (min-width: $screen-sm-min) {
    .supplier-image {
      height: 160px;

      img {
        max-width: 280px;
      }
    }
  }
}

.panel--phone-block {
  @media (min-width: $screen-sm-min) {
    .panel-body {
      min-height: 80px;
      padding-right: 200px;
      position: relative;

      .btn {
        bottom: $panel-body-padding;
        right: $panel-body-padding;
        position: absolute;
      }
    }
  }
}

.panel-onderwerp a.list-group-item {
  text-decoration: none;
}

/*------------------------------------*\
    $ANIMATIES
\*------------------------------------*/
@media (min-width: $screen-sm-min) {
  .animate-panels > .trigger-wrapper {
    @include animation(fadeIn ease-out 1s 2s both);
  }
}

.home-panels {
  padding-bottom: 24px;
}

/* Aanpassing specifiek voor stappenplan terugleveren */
.panel-vertical .panel-body--step .col-sm-9,
.panel-vertical .panel-body--step .col-sm-12,
.panel-vertical .ab-colstep .col-sm-9,
.panel-vertical .ab-colstep .col-sm-12 {
  padding-left: 0px;
  padding-right: 0px;
}

.panel-default {
  .panel__sub-heading {
    color: $swatchPurple;
  }

  &.panel--illustrated {
    .panel-body {
      background: $colorWhite;
    }
  }
}

.panel-primary {
  &.panel--illustrated {
    background-color: $panel-primary-border;
    border-width: 0;
  }
}

.panel-secondary {
  @include panel-variant($swatchBlue, $panel-default-text, $panel-default-heading-bg, $panel-default-border);

  &.panel--illustrated {
    background-color: $swatchBlue;
    border-width: 0;
  }
}

.panel-alternate {
  @include panel-variant($swatchBlueDark, $panel-default-text, $panel-default-heading-bg, $panel-default-border);

  &.panel--illustrated {
    background-color: $swatchBlueDark;
    border-width: 0;
  }
}

.panel--filled-primary {
  background-color: $panel-primary-border;
  border-width: 0;

  &,
  .panel__title,
  .box-header,
  .panel-title {
    color: #fff;

    .form-group.has-error label {
      color: #fff;
    }
  }

  .checkbox {
    padding-left: 10px;

    label {
      color: #fff;
    }
  }
}

.panel--faq {
  h2.panel-heading {
    color: $colorPrimary;
  }
}

.panel--faq-search {
  h2.panel-heading {
    font-size: $font-size-base;
    color: $colorPrimary;
    border: none;

    a:link {
      float: right;
      @media (max-width: $screen-xs-max) {
        display: none;
      }
    }
  }

  .btn {
    margin: 5px 3px;
  }
}

.unbordered-heading-wrapper {
  .panel-heading {
    border-bottom-width: 0;
  }

  .wizard-step {
    padding-top: 20px;
  }
}

.attention-wrapper {
  h2 {
    color: $colorAttention;
  }

  .main-buttons .btn {
    //@include button-variant(#fff, $btn-primary-bg, $btn-primary-border);
    @extend .btn-primary;
  }
}

.shadowed-wrapper {
  .panel-default {
    box-shadow: 2px 3px 10px 3px rgba(187, 187, 187, .75);
  }
}

//specific panel styling
.trigger8 {
  .panel-body {
    min-height: 300px;
  }

  .image {
    position: absolute;
    top: 9px;
    right: 12px;

    img {
      margin: auto;
      display: block;
    }
  }

  .trigger-link a {
    color: $swatchBlue;
    padding-left: 0;

    &:hover {
      color: #fff;
    }
  }

  @media (min-width: $screen-sm-min) {
    .trigger-link {
      bottom: 19px;
      position: absolute;
      right: 12px;
    }
  }
}

//Show hidden checkbox only on this page (Disturbance Detail)
.node-type-liander-disturbance-detail {
  .trigger8 .checkbox {
    display: block !important;
    visibility: visible !important;
  }
}

.trigger7 {
  .panel-secondary.panel--illustrated {
    height: auto;
    padding-bottom: 0;
  }

  .btn {
    position: static;
  }
}

.future-disturbances {

  label {
    white-space: nowrap;
  }
}

.trigger-location {
  @media (max-width: $screen-xs-max) {
    .panel-body {
      hr {
        padding-bottom: 10px;
      }
      p:last-child {
        padding-bottom: 10px;
      }
    }
  }
}

.panel-group {
  .panel {
    border-radius: 0 $panel-border-radius $panel-border-radius;
  }
}
